import axiosCustom from '../axios'

export default class UserServices {
  static getAllUsers () {
    return axiosCustom.get('/admin/user')
  }

  static getUsers (data) {
    return axiosCustom.get('/admin/user', { params: data.params })
  }

  static getUserById (data) {
    return axiosCustom.get(`/user/${data._id}`)
  }

  static getUserRestaurants () {
    return axiosCustom.get('/user/stores')
  }

  static addUser (data) {
    return axiosCustom.post('/user', data)
  }

  static updateUser (id, data) {
    return axiosCustom.patch('/user', data)
  }

  static updateUserPassword (data) {
    return axiosCustom.patch('/user/changePassword', data)
  }

  static deleteUser (data) {
    return axiosCustom.delete(`/admin/user/${data._id}`)
  }

  static sharePage (data) {
    return axiosCustom.post('/user/share-page', data)
  }

  /// /Servicios///////
  static getServices (data) {
    return axiosCustom.get('/user/services', { params: data.params })
  }

  /// /Lista de deseo///////
  static getWishList () {
    return axiosCustom.get('/user/wishList')
  }

  static addToWishList (data) {
    return axiosCustom.patch('/user/wishList?addOffer=' + data)
  }

  static removeToWishList (data) {
    return axiosCustom.patch('/user/wishList?removeOffer=' + data)
  }

  /// /ROLES///////
  static getRoles () {
    return axiosCustom.get('/admin/roles')
  }

  static changeRole (data) {
    return axiosCustom.patch(`/admin/user/${data._id}/changeRole`, { role: data.role })
  }

  static deleteMyAccount () {
    return axiosCustom.delete('/user/')
  }
}
