<template>
  <div class="observer"></div>
</template>

<script>
export default {
  name: 'InfinityScroll',
  data () {
    return {
      observer: null
    }
  },
  mounted () {
    this.observer = new IntersectionObserver(entry => {
      if (entry && entry[0].isIntersecting) {
        this.$emit('intersect')
      }
    })
    this.observer.observe(this.$el)
  }
}
</script>

<style lang="scss" scoped>
.observer{
  height: 1px;
}
</style>
