<template>
    <v-card class="header-toolbar">
      <v-app-bar color="white" absolute elevate-on-scroll style="position: fixed; z-index: 8; width: 100%;">

        <v-row :class="$vuetify.breakpoint.mobile && $vuetify.breakpoint.xs ? 'ml-4 mr-2' : 'ml-4 mr-4'" align="center" justify="center">
          <router-link to="/">
            <v-toolbar-title>
              <!-- <v-img height="35" max-height="35" width="92.418" max-width="92.418" src="../../public/img/Identidad_AlaCasa.svg" lazy-src="../../public/img/Identidad_AlaCasa.svg"></v-img> -->
              <h3>Vendo</h3>
            </v-toolbar-title>
          </router-link>

          <v-spacer></v-spacer>

          <v-btn icon rounded v-if="!isAdmin">
            <v-icon>mdi-magnify</v-icon>
          </v-btn>
          <v-btn icon to="/calendar" rounded>
            <v-icon>mdi-calendar</v-icon>
          </v-btn>
          <v-btn text to="/stores" rounded>
            <v-icon class="mr-2">mdi-store-outline</v-icon>
          </v-btn>
          <v-app-bar-nav-icon @click.stop="showDrawer"></v-app-bar-nav-icon>

        </v-row>
      </v-app-bar>
    </v-card>

    <!-- <Breadcrumbs /> -->
</template>

<script>
// import Breadcrumbs from '@/components/Breadcrumbs'
import userServices from '@/axios/user.services'
import router from '../../router'

export default {
  name: 'AppBar',
  components: {
    // Breadcrumbs
  },
  data: () => ({
    search: '',
    drawer: false,
    isLogged: !!localStorage.getItem('store_user'),
    group: null,
    dialogDelete: false,
    // userRol: JSON.parse(localStorage.getItem('store_user')).user.role.name,
    userRol: 'Admin',
    items: [
      { title: 'INICIO', icon: 'mdi-home-city', url: 'dashboard', roles: ['Admin'] },
      { title: 'MI PERFIL', icon: 'mdi-home-city', url: '/administration/profile', roles: [] },
      { title: 'MI VENTA', icon: 'mdi-home-city', url: '/administration/my-store', roles: [] },
      { title: 'MI CALENDARIO', icon: 'mdi-home-city', url: '/administration/my-calendar', roles: [] }
      // { title: 'PEDIDOS', icon: 'mdi-home-city', url: 'pedidos', roles: ['Admin', 'Basic', 'Businessman'] },
      // { title: 'USUARIOS', icon: 'mdi-contacts', url: 'usuarios', roles: ['Admin'] },
      // { title: 'MIS CONTACTOS', icon: 'mdi-user', url: 'contactos', roles: ['Basic', 'Businessman'] },
      // { title: 'REPORTES', icon: 'mdi-account', url: 'reportes', roles: ['Admin', 'Operator'] },
      // { title: 'ORDENES PENDIENTES', icon: 'mdi-account', url: 'pendientes', roles: ['Admin', 'Operator'] }
      // { title: 'RDENES ANTERIORES', icon: 'mdi-account-group-outline', url: 'operations-history' },
    ],
    sizes: {
      contentWidth: '70%',
      height: 'auto !important'
    }
  }),
  created () {
    this.$store.dispatch('loadLocalUserInformation')

    const cart = localStorage.getItem('cubamax_cart')
    if (cart != null) {
      this.$store.dispatch('setCart', JSON.parse(cart))
    }

    if (this.isLoggedIn() && !this.isAdmin) {
      userServices.getWishList().then((result) => {
        if (result) {
          this.$store.dispatch('setFavoriteProducts', result.data.wishList.offers)
        }
      }).catch(() => {

      })
    }
  },
  watch: {
    '$route' (to, from) {
      if (localStorage.getItem('store_user')) {
        this.isLogged = true
      } else {
        this.isLogged = false
      }
    },
    group () {
      this.drawer = false
    }
  },
  computed: {

    notificationAmoutn: {
      get () {
        return this.$store.getters.cart().length
      }
    },

    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': {
          // this.drawer = false
          return true
        }
        case 'sm': {
          // this.drawer = false
          return (this.$vuetify.breakpoint.mobile && window.innerWidth < 630) || false
        }
        case 'md': {
          // this.drawer = true
          return false
        }
        case 'lg': {
          // this.drawer = true
          return false
        }
        case 'xl': {
          // this.drawer = true
          return false
        }
      }
    },
    isAdmin () {
      return this.$store.getters.isAdmin()
    },
    absolute () {
      return this.$route.path.includes('ajustes') || this.$route.path.includes('administracion')
    }
  },
  methods: {
    isLoggedIn () {
      return this.$store.getters.userIsLogued()
    },

    submitSearch () {
      alert('Search')
    },

    logout () {
      // localStorage.clear()
      this.$store.dispatch('logOut')
      this.$store.dispatch('clearCart')
      if (this.$route.name !== 'stores') {
        router.replace('/')
      } else {
        this.isLogged = false
      }
    },

    show (item) {
      return item.roles.indexOf(this.userRol) !== -1
    },

    showDrawer () {
      this.$store.dispatch('showDrawer')
    },

    deleteAccountConfirm () {
      userServices.deleteMyAccount().then((result) => {
        if (result) {
          this.$store.dispatch('logOut')
          this.$store.dispatch('clearCart')
          if (this.$route.name !== 'stores') {
            router.replace('/stores')
          } else {
            this.isLogged = false
          }
        }
      }).catch(() => {
        this.$toast.error('No se pudo eliminar el storee')
      })
    },

    closeDelete () {
      this.dialogDelete = false
    }
  }
}
</script>

