<template>
<v-card class="overflow-hidden header-toolbar">
    <v-app-bar
      absolute
      color="white"
      elevate-on-scroll
      scroll-target="#scrolling-techniques-7"
    >
      <BackButton />

      <v-toolbar-title>Mi cuenta</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn text @click="updateProfile">Guardar</v-btn>
    </v-app-bar>
    <v-sheet
      id="scrolling-techniques-7"
      class="overflow-y-auto"
      max-height="100vh"
    >
      <v-container id="profile-container" class="pt-11">

          <!-- <Breadcrumbs /> -->

          <ValidationObserver ref="observer">
            <form autocomplete="off" @submit.prevent="updateProfile">
              <v-container :style="`padding: 12px ${$vuetify.breakpoint.mobile ? '0' : '1rem'};`">

                  <h2 class="text-bolder my-6 mx-3">Datos de la cuenta</h2>

                  <!-- Nombre y apellidos -->
                  <v-row justify="space-between">
                    <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="firstname"
                          rules="required"
                        >
                        <!-- <label for="name">Nombre*</label> -->
                        <v-text-field
                          id="firstname"
                          v-model="user.firstname"
                          label="Nombre *"
                          placeholder="Nombre completo"
                          outlined
                          dense
                          class="mx-auto rounded-lg"
                          :error-messages="errors"
                          :hide-details="errors.length > 0 ? false : true"
                        ></v-text-field>
                      </ValidationProvider>
                    </v-col>
                    <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="lastname"
                          rules="required"
                        >
                        <!-- <label for="lastName">Apellidos*</label> -->
                      <v-text-field
                        id="lastname"
                        v-model="user.lastname"
                        label="Apellidos *"
                        placeholder="Apellidos completo"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col>

                  <!-- Correo y movil -->
                    <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="email"
                          rules="required"
                        >
                        <!-- <label for="email">Correo electrnio*</label> -->
                        <v-text-field
                          id="email"
                          v-model="user.email"
                          label="Correo electrnio *"
                          placeholder="Escriba su correo"
                          outlined
                          dense
                          class="mx-auto rounded-lg"
                          :error-messages="errors"
                          :hide-details="errors.length > 0 ? false : true"
                        ></v-text-field>
                      </ValidationProvider>
                    </v-col>

                  <!-- direccion y telefonos -->

                    <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="phone"
                          rules="required"
                        >
                        <!-- <label for="phone">Telfono Mvil</label> -->
                      <v-text-field
                        id="phone"
                        v-model="user.phone"
                        label="Telfono Mvil *"
                        placeholder="Escriba su nmero"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col>

                    <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="houseNumber"
                        >
                        <!-- <label for="address1">Telfono Fijo</label> -->
                      <v-text-field
                        id="houseNumber"
                        v-model="user.profile.housePhone"
                        label="Telfono Fijo"
                        placeholder="Nmero"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col>

                    <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                      <ValidationProvider
                        v-slot="{ errors }"
                        name="address1"
                        rules="required"
                      >
                        <!-- <label for="address1">Direccin</label> -->
                        <v-text-field
                          id="address1"
                          v-model="user.profile.address.address1"
                          label="Direccin"
                          placeholder="Direccin de facturacin"
                          outlined
                          dense
                          class="mx-auto rounded-lg"
                          :error-messages="errors"
                          :hide-details="errors.length > 0 ? false : true"
                        ></v-text-field>
                      </ValidationProvider>
                    </v-col>

                  <!-- Ciudad y estado -->
                    <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                      <ValidationProvider
                        v-slot="{ errors }"
                        name="state"
                        rules="required"
                      >
                      <!-- <label for="state">Estado</label> -->
                      <v-select
                        id="state"
                        v-model="user.profile.address.province"
                        :items="provinces"
                        item-text="name"
                        item-value="name"
                        label="Provincia"
                        placeholder="Seleccionar provincia"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        no-data-text="No hay datos"
                        @change="loadCities"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-select>
                    </ValidationProvider>
                    </v-col>
                    <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="municipality"
                          rules="required"
                        >
                        <!-- <label for="city">Municipio</label> -->
                        <v-select
                          id="municipality"
                          v-model="user.profile.address.municipality"
                          :items="municipalities"
                          label="Municipio"
                          placeholder="Seleccionar ciudad"
                          outlined
                          dense
                          class="mx-auto rounded-lg"
                          no-data-text="No hay datos"
                          :error-messages="errors"
                          :hide-details="errors.length > 0 ? false : true"
                        ></v-select>
                      </ValidationProvider>
                    </v-col>

                  <!-- Genero -->
                    <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="gender"
                        >
                        <!-- <label for="city">Municipio</label> -->
                        <v-select
                          id="gender"
                          v-model="user.profile.gender"
                          :items="genders"
                          item-value="id"
                          item-text="text"
                          label="Gnero"
                          outlined
                          dense
                          class="mx-auto rounded-lg"
                          no-data-text="No hay datos"
                          :error-messages="errors"
                          :hide-details="errors.length > 0 ? false : true"
                        ></v-select>
                      </ValidationProvider>
                    </v-col>

                  </v-row>

                  <h4 class="text-bolder px-3 pt-7 pb-0">Cambiar contrasea</h4>
                  <v-row justify="space-between">
                    <v-col cols="12" md="4" sm="4" xs="12">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="oldPassword"
                          :rules="newPassword !== null && newPassword !== '' ? 'required|min:8' : 'min:8'"
                        >
                      <v-text-field
                        ref="oldPassword"
                        id="oldPassword"
                        v-model="oldPassword"
                        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show1 ? 'text' : 'password'"
                        placeholder="Contrasea actual"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                        @click:append="show1 = !show1"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col>
                    <v-col cols="12" md="4" sm="4" xs="12">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="password"
                          :rules="oldPassword != null && oldPassword != '' ? 'required|min:8' : 'min:8'"
                        >
                      <v-text-field
                        ref="password"
                        id="password"
                        v-model="newPassword"
                        :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show2 ? 'text' : 'password'"
                        placeholder="Nueva contrasea"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                        @click:append="show2 = !show2"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col>
                    <v-col cols="12" md="4" sm="4" xs="12">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="password_confirm"
                          :rules="newPassword != null && newPassword != '' ? 'required|min:8|confirmed:password' : 'confirmed:password'"
                        >
                      <v-text-field
                        id="password_confirm"
                        v-model="passwordConfirm"
                        :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show3 ? 'text' : 'password'"
                        placeholder="Confirmar nueva contrasea"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                        @click:append="show3 = !show3"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col>
                  </v-row>

              </v-container>
            </form>
          </ValidationObserver>
        </v-container>
    </v-sheet>
  </v-card>
</template>

<script>
import userServices from '@/axios/user.services'
import states from '@/nomencladores/states.json'
import BackButton from '@/components/BackButton.vue'
import moment from 'moment'
import provinces from '@/nomencladores/provinces.json'

export default {
  name: 'Profile',
  components: {
    BackButton
  },
  data: function () {
    return {
      date: null,
      date_menu: null,
      user: JSON.parse(localStorage.getItem('store_user')).user,
      oldPassword: null,
      newPassword: null,
      passwordConfirm: null,
      provinces: provinces,
      province: null,
      municipality: null,
      roles: [],
      show1: false,
      show2: false,
      show3: false,
      genders: [{ id: 'F', text: 'Femenino' }, { id: 'M', text: 'Masculino' }]
    }
  },
  created () {
    if (!this.user.profile) {
      this.user.profile = {
        address: {
          houseNumber: null,
          streetDirection: null,
          streetName: null,
          streetSuffix: null,
          city: null,
          state: null,
          zip: null
        },
        housePhone: null,
        dateBirth: null,
        gender: null,
        notifications: null,
        telegram: {
          username: null,
          chatId: null
        }
      }
    }

    if (this.user.profile.dateBirth) {
      this.user.profile.dateBirth = moment(this.user.profile.dateBirth).format('L')
    }

    if (this.user.profile.address.state) {
      this.loadCities()
    }
  },
  mounted () {
    for (const key in states) {
      this.states.push(key)
    }
  },
  watch: {
    date (val) {
      this.user.profile.dateBirth = this.formatDate(this.date)
    }
  },
  computed: {
    municipalities: function () {
      if (this.user.profile.address.province) {
        return this.provinces.find(p => p.name === this.user.profile.address.province).municipalities
      }
      return []
    },
    isRestaurantOwner () {
      return this.user.role.name === 'RestaurantOwner'
    }
  },
  methods: {
    loadCities () {
      this.cities = states[this.user.profile.address.state]
    },
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    updateProfile () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          // delete this.user.profile.telegram
          const id = this.user._id
          delete this.user._id
          userServices.updateUser(id, this.user).then((response) => {
            if (response) {
              if (this.oldPassword != null && this.passwordConfirm != null) {
                userServices.updateUserPassword({
                  oldPassword: this.oldPassword,
                  newPassword: this.passwordConfirm
                }).then((changeResponse) => {
                  if (changeResponse) {
                    this.$toast.success('Perfil actualizado!')
                  }
                }).catch(() => {
                  this.$toast.error('Error al actualizar contrasea')
                })
              } else {
                this.$toast.success('Perfil actualizado!')
              }

              var cubamaxUser = JSON.parse(localStorage.getItem('store_user'))
              cubamaxUser.user = this.user
              localStorage.setItem('store_user', JSON.stringify(cubamaxUser))
            }
          }).catch(() => {
            this.$toast.error('Error al actualizar perfil')
          })
        }
      })
    },
    cancel () {
      this.$emit('cancel')
    }
  }
}
</script>

<style lang="scss" scoped>
.row{
  margin: 7px 0 !important;
}
h3{
  text-transform: uppercase;
}

</style>
