const mutations = {

  setCart (state, cart) {
    state.cart = cart
  },

  setLocation (state, location) {
    state.province = location.province
    state.municipality = location.municipality
    localStorage.setItem('store_search', JSON.stringify({
      province: state.province,
      municipality: state.municipality,
      department: state.department
    }))
  },

  setDepartment (state, department) {
    state.department = department
    localStorage.setItem('store_search', JSON.stringify({
      province: state.province,
      municipality: state.municipality,
      department: state.department
    }))
  },

  setCategory (state, category) {
    state.category = category
  },

  setSubCategory (state, subCategory) {
    state.subCategory = subCategory
  },

  addToCart (state, product) {
    // const i = state.cart.findIndex(stored => stored._id === product._id)
    // if (i !== -1) {
    //   state.cart[i].amount = parseInt(state.cart[i].amount) + 1
    // } else {
    //   product.amount = 1
    // }
    state.cart.push(product)
    localStorage.setItem('cubamax_cart', JSON.stringify(state.cart))
  },

  removeFromCart (state, product) {
    state.cart = state.cart.filter(item => item._id !== product._id)
    localStorage.setItem('cubamax_cart', JSON.stringify(state.cart))
  },

  reduceAmountOfProductFromCart (state, product) {
    const i = state.cart.findIndex(stored => stored._id === product._id)
    if (i !== -1) {
      state.cart.splice(i, 1)
      localStorage.setItem('cubamax_cart', JSON.stringify(state.cart))
    }
  },

  removeRestaurantProductsFromCart (state, storeId) {
    state.cart = state.cart.filter(item => item.store._id !== storeId)
    localStorage.setItem('cubamax_cart', JSON.stringify(state.cart))
  },

  clearCart (state) {
    state.cart = []
    localStorage.setItem('cubamax_cart', JSON.stringify(state.cart))
  },

  setFavoriteProducts (state, favoriteProducts) {
    state.favoriteProducts = favoriteProducts
  },

  addToFavoriteProducts (state, product) {
    if (state.favoriteProducts.filter(item => item._id === product._id).length === 0) {
      state.favoriteProducts.push(product)
    }
  },

  removeFromFavoriteProducts (state, product) {
    state.favoriteProducts = state.favoriteProducts.filter(item => item._id !== product._id)
  },

  clearFavoriteProducts (state) {
    state.favoriteProducts = []
  },

  addEvent (state, event) {
    event.id = Math.floor((10 - 0 + 1) * Math.random()) + 0
    state.events.push(event)
  },

  createStore (state, store) {
    state.store = store
  }
}

const actions = {

  setCart ({ commit }, cart) {
    commit('setCart', cart)
  },

  setLocation ({ commit }, location) {
    commit('setLocation', location)
  },

  setDepartment ({ commit }, department) {
    commit('setDepartment', department)
  },

  setCategory ({ commit }, category) {
    commit('setCategory', category)
  },

  setSubCategory ({ commit }, subCategory) {
    commit('setSubCategory', subCategory)
  },

  setFavoriteProducts ({ commit }, product) {
    commit('setFavoriteProducts', product)
  },

  updateProductDetails ({ commit }, product) {
    commit('updateProductDetails', product)
    router.replace('/product-details')
  },

  addToCart ({ commit }, product) {
    commit('addToCart', product)
  },

  removeFromCart ({ commit }, product) {
    commit('removeFromCart', product)
  },

  reduceAmountOfProductFromCart ({ commit }, product) {
    commit('reduceAmountOfProductFromCart', product)
  },

  removeRestaurantProductsFromCart ({ commit }, storeId) {
    commit('removeRestaurantProductsFromCart', storeId)
  },

  clearCart ({ commit }) {
    commit('clearCart')
  },

  addToFavoriteProducts ({ commit }, product) {
    commit('addToFavoriteProducts', product)
  },

  removeFromFavoriteProducts ({ commit }, product) {
    commit('removeFromFavoriteProducts', product)
  },

  addEvent ({ commit }, event) {
    commit('addEvent', event)
  },

  createStore ({ commit }, store) {
    commit('createStore', store)
  }

}

const getters = {
  cart: (state) => () => {
    return state.cart
  },
  favoriteProducts: (state) => () => {
    return state.favoriteProducts
  },
  checkProductOnCart: (state) => (id) => {
    // const i = state.cart.findIndex(stored => stored._id === id)
    // if (i !== -1) {
    //   return state.cart[i].amount
    // } else {
    //   return 0
    // }
    return state.cart.filter(product => product._id === id).length
  },
  checkProductOnFavorites: (state) => (id) => {
    return state.favoriteProducts.filter(product => product._id === id).length
  },
  products: (state) => () => {
    return state.products
  },
  getProduct: (state) => (index) => {
    return state.products[index]
  },

  stores: (state) => () => {
    return state.stores
  },

  getLocation: (state) => () => {
    return {
      province: state.province,
      municipality: state.municipality
    }
  },

  getDepartment: (state) => () => {
    return state.department
  },

  categories: (state) => () => {
    return state.categories
  },

  events: (state) => () => {
    return state.events
  },

  userStore: (state) => () => {
    return state.store
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
