<template>
  <v-main>
      <v-container id="recover-container" :style="breakpoint ? 'height: 130vh !important;' : 'height: auto'">
        <v-col id="login-form" cols="12" md="7" sm="10" xs="10">
          <h1 class="text-bolder text-center mb-8">Recuperar constrasea</h1>
          <v-card class="rounded-xl">
            <v-col align-self="center">
              <ValidationObserver ref="observer">
                <v-row align="center">
                  <v-col cols="12" md="6" sm="12" xs="12">
                    <!-- <ValidationProvider
                      v-slot="{ errors }"
                      name="email"
                      rules="required"
                    > -->
                      <label class="text-left" for="email">Usuario</label>
                      <v-text-field
                        ref="email"
                        id="email"
                        v-model="user.email"
                        placeholder="Correo"
                        outlined
                        dense
                        full-width
                        readonly
                        class="mx-auto rounded-lg"
                        hide-details
                      >
                      </v-text-field>
                    <!-- </ValidationProvider> -->
                  </v-col>

                  <v-col cols="12" md="6" sm="12" xs="12">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="code"
                      rules="required"
                    >
                      <label for="code">Cdigo</label>
                      <v-text-field
                        ref="code"
                        id="code"
                        v-model="code"
                        placeholder="Cdigo de 6 dgitos"
                        outlined
                        full-width
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      >
                      </v-text-field>
                    </ValidationProvider>
                  </v-col>

                  <v-col cols="12" md="6" sm="12" xs="12">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="password"
                      rules="required|min:8"
                    >
                      <label class="text-left" for="password">Contrasea nueva</label>
                      <v-text-field
                        ref="password"
                        id="password"
                        v-model="user.password"
                        placeholder="Contrasea de 8 caracteres como mnimo"
                        outlined
                        dense
                        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show1 ? 'text' : 'password'"
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                        @click:append="show1 = !show1"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>

                  <v-col cols="12" md="6" sm="12" xs="12">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="passwordConfirmation"
                      rules="required|min:8|confirmed:password"
                    >
                      <label class="text-left" for="passwordConfirmation">Confirmar su contrasea</label>
                      <v-text-field
                        ref="passwordConfirmation"
                        id="passwordConfirmation"
                        v-model="passwordConfirmation"
                        placeholder="Repita todos los caracteres"
                        outlined
                        dense
                        :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show2 ? 'text' : 'password'"
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                        @click:append="show2 = !show2"
                      >
                      </v-text-field>
                    </ValidationProvider>
                  </v-col>
                </v-row>
                <v-row align="center" justify="space-around">
                  <v-col align="center">
                    <p class="text-info"><span class="link" @click="reSendEmail">Volver a enviar el cdigo</span></p>
                  </v-col>
                </v-row>
                <v-row align="center" justify="space-around">
                  <v-col align="center">
                    <v-btn class="rounded-lg text-bold shadow" color="secondary" @click="updatePassword">Cambiar contrasea</v-btn>
                  </v-col>
                </v-row>
                <v-row align="center" justify="space-around">
                  <v-col align="center">
                    <p>Si ya tienes una cuenta en CubaMax, <router-link class="text-info" to="/iniciar">Inicia sesin</router-link></p>
                    <!-- <p>Si eres nuevo en CubaMax, <router-link class="text-info" to="/registrarse">Regstrate</router-link></p> -->
                  </v-col>
                </v-row>
              </ValidationObserver>
            </v-col>
          </v-card>
        </v-col>
      </v-container>
  </v-main>
</template>

<script>
import authenticationServices from '@/axios/authentication.services'
import router from '../../router'

export default {
  name: 'ForgotPassword',
  data () {
    return {
      code: null,
      user: {
        email: null,
        password: null
      },
      show1: false,
      show2: false,
      passwordConfirmation: null
    }
  },
  created () {
    if (!this.$route.params.user) {
      router.replace('/iniciar')
    } else {
      this.user = this.$route.params.user
    }
  },
  computed: {
    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': {
          return true
        }
        case 'sm': {
          return (this.$vuetify.breakpoint.mobile && window.innerWidth < 830) || false
        }
        case 'md': {
          return false
        }
        case 'lg': {
          return false
        }
        case 'xl': {
          return false
        }
      }
    }
  },
  methods: {
    updatePassword () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          authenticationServices.forgotPassword({ code: this.code, password: this.user.password }).then((result) => {
            if (result) {
              this.$toast.success('Contrasea actualizada!')
              router.replace('/iniciar')
            }
          }).catch(() => {
            this.$toast.error('Error al verificar su cdigo!')
          })
        }
      })
    },
    reSendEmail () {
      authenticationServices.reSendEmail({ type: 2, email: this.user.email }).then((result) => {
        if (result) {
          this.$toast.success('Se ha enviado un cdigo a su correo')
        }
      }).catch(() => {
        this.$toast.error('No se pudo enviar el cdigo de verificacin')
      })
    }
  }
}
</script>

<style lang="scss" scoped>
#login-form{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}

.row{
  margin: 0
}

.link{
  cursor: pointer;
}

input{
  text-align: center !important;
}
</style>
