import AppBar from '@/components/AppBar/AppBar'
import SideMenu from '@/components/AppBar/SideMenu'
import SearchLocationDialog from '@/components/SearchLocationDialog.vue'
// import Footer from '@/components/Footer'

export default {
  name: 'App',
  metaInfo: {
    title: 'Alacasa',
    meta: [
      { name: 'description', content: 'Alacasa es un sitio que brinda servicios para que usted pueda hacer pedidos a domicilio para sus familiares en Cuba desde el exterior' },
      { name: 'keywords', content: 'Alacasa, Cubamax, domicilio, comida, restaurante, restaurantes, pago online' },
      { property: 'og:title', content: 'Alacasa' },
      { property: 'og:site_name', content: 'Alacasa' }
    ],
    htmlAttrs: {
      lang: 'es',
      amp: true
    }
  },
  components: {
    AppBar,
    SideMenu,
    SearchLocationDialog
    // Footer
  },
  data: () => ({

  }),
  created () {
    this.$store.dispatch('loadLocalUserInformation')
  },
  computed: {
    hide () {
      return this.$route.name === 'login' || this.$route.name === 'signup' ||
             this.$route.name === 'confirmation' || this.$route.name === 'forgotPassword' ||
             this.$route.path !== '/'
    },

    appBarMargin () {
      return this.$route.path.includes('ajustes') || this.$route.path.includes('administracion')
    },

    backgroundAutosize () {
      return this.$route.name === 'restaurants' || this.$route.name === 'restaurant'
    },
    isAdmin () {
      return this.$store.getters.isAdmin()
    }
  }
}
</script>

