<template>
  <v-navigation-drawer
    v-model="showDrawer"
    absolute
    right
    temporary
    width="75%"
    style="position: fixed; top: 55px;"
    >

    <v-list dense>

      <v-list-item-group
        v-model="group"
      >
          <v-btn block text @click="showSearchLocationDialog" style="height: 60px !important;" v-if="searchLocation">
          <template v-slot:default>
            <v-row>
              <v-col>
                <label for="">{{ searchLocation.province }}</label>
                <p class="mb-0">{{ searchLocation.municipality }}</p>
              </v-col>
              <v-icon>mdi-filter-outline</v-icon>
            </v-row>
          </template>
        </v-btn>
        <v-divider class="mb-3"></v-divider>

        <v-row justify="end" class="pr-5" style="position: absolute; right: 5px; z-index: 9"  v-if="isLogged">
          <v-btn icon to="/" rounded large>
            <v-badge
              :content="notificationAmoutn"
              :value="notificationAmoutn"
              color="yellow darken-4"
              overlap
            >
              <v-icon>mdi-bell-outline</v-icon>
            </v-badge>
          </v-btn>
        </v-row>

        <v-list-item link to="/login" style="display: block;" v-if="!isLogged">
          <v-list-item-content>
            <v-list-item-title class="text-bolder">INICIAR SESIN</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-list-item
          v-for="item in items"
          :key="item.title"
          :style="isLogged && show(item) ? 'display: block' : 'display: none'"
          :to="item.url"
        >
          <!-- <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon> -->

          <v-list-item-content>
            <v-list-item-title class="text-bolder">{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <!-- <v-divider class="mx-6 my-5" v-if="!isAdmin"></v-divider>

        <v-list-item style="display: block;" v-if="!isAdmin">
          <v-list-item-content>
            <v-list-item-title>
              <v-btn text to="/carrito">
                <v-icon class="pr-2">mdi-cart-variant</v-icon> <span class="text-bolder" style="font-size: 11px">Carrito de Compras</span>
              </v-btn>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item> -->

        <v-divider class="mx-6 my-5" v-if="isLogged"></v-divider>

        <v-list-item link style="display: block;" v-if="isLogged" @click="logout">
          <v-list-item-content>
            <v-list-item-title class="text-bolder">CERRAR SESIN</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-divider class="mx-6 my-5" v-if="isLogged && !isAdmin"></v-divider>

        <v-list-item style="display: block;" v-if="isLogged && !isAdmin" @click="dialogDelete = true">
          <v-list-item-content>
            <v-list-item-title class="text-bolder">
                <v-icon class="pr-2">mdi-trash-can-outline</v-icon>
                ELIMINAR CUENTA
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>

      </v-list-item-group>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import userServices from '@/axios/user.services'
import router from '../../router'

export default {
  name: 'Settings',
  data () {
    return {
      dialogDelete: false,
      // userRol: JSON.parse(localStorage.getItem('store_user')).user.role.name,
      userRol: 'Admin',
      items: [
        { title: 'MI PERFIL', icon: 'mdi-home-city', url: '/administration/profile', roles: ['Admin'] },
        { title: 'MI VENTA', icon: 'mdi-home-city', url: '/administration/my-store', roles: ['Admin'] },
        { title: 'MI CALENDARIO', icon: 'mdi-home-city', url: '/administration/my-calendar', roles: ['Admin'] }
        // { title: 'PEDIDOS', icon: 'mdi-home-city', url: 'pedidos', roles: ['Admin', 'Basic', 'Businessman'] },
        // { title: 'USUARIOS', icon: 'mdi-contacts', url: 'usuarios', roles: ['Admin'] },
        // { title: 'MIS CONTACTOS', icon: 'mdi-user', url: 'contactos', roles: ['Basic', 'Businessman'] },
        // { title: 'REPORTES', icon: 'mdi-account', url: 'reportes', roles: ['Admin', 'Operator'] },
        // { title: 'ORDENES PENDIENTES', icon: 'mdi-account', url: 'pendientes', roles: ['Admin', 'Operator'] }
        // { title: 'RDENES ANTERIORES', icon: 'mdi-account-group-outline', url: 'operations-history' },
      ],
      group: null,
      drawer: false,
      sizes: {
        contentWidth: '70%',
        height: 'auto !important'
      }
    }
  },
  watch: {
    // '$route' (to, from) {
    //   this.$store.dispatch('showDrawer', false)
    // },
    group () {
      this.$store.dispatch('showDrawer')
    }
  },

  computed: {
    notificationAmoutn: {
      get () {
        return this.$store.getters.cart().length
      }
    },

    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': {
          this.drawer = false
          this.sizes.contentWidth = '100%'
          this.sizes.height = '100% !important'
          return true
        }
        case 'sm': {
          this.drawer = false
          this.sizes.contentWidth = '90%'
          this.sizes.height = '100% !important'
          return true
        }
        case 'md': {
          this.drawer = true
          this.sizes.contentWidth = '70%'
          this.sizes.height = 'auto !important'
          return false
        }
        case 'lg': {
          this.drawer = true
          this.sizes.contentWidth = '70%'
          this.sizes.height = 'auto !important'
          return false
        }
        case 'xl': {
          this.drawer = true
          this.sizes.contentWidth = '70%'
          this.sizes.height = 'auto !important'
          return false
        }
      }
    },
    isAdmin () {
      return this.$store.getters.isAdmin()
    },
    showDrawer () {
      return this.$store.getters.showDrawer()
    },
    isLogged () {
      return this.$store.getters.userIsLogued()
    },
    searchLocation () {
      return this.$store.getters.getLocalStoreSearchData()
    }
  },
  methods: {
    show (item) {
      return item.roles.indexOf(this.userRol) !== -1
    },

    showSearchLocationDialog () {
      this.$store.dispatch('showDrawer')
      this.$store.dispatch('showSearchLocationDialog')
    },

    deleteAccountConfirm () {
      userServices.deleteMyAccount().then((result) => {
        if (result) {
          this.$store.dispatch('logOut')
          this.$store.dispatch('clearCart')
          if (this.$route.name !== 'stores') {
            router.replace('/stores')
          } else {
            this.isLogged = false
          }
        }
      }).catch(() => {
        this.$toast.error('No se pudo eliminar el storee')
      })
    },

    closeDelete () {
      this.dialogDelete = false
    },

    logout () {
      this.$store.dispatch('logOut')
      router.replace('/')
      this.isLogged = false
    }
  }
}
</script>

<style lang="scss" scoped>
.v-navigation-drawer{
  height: 100% !important;
}
.row{
  margin: 0;
}
.active-route{
  background: rgba(48,153,153,1);
  color: white !important;
  box-shadow: 1px 1px 32px rgba(48, 153, 153, 0.322);
}
.v-list-item__title {
  font-size: 16px !important;
}
</style>
