<template>
  <div>

    <v-dialog v-if="isMobile"
      v-model="dialog"
      id="product-option-dialog"
      max-width="70%"
    >
      <template v-slot:activator="{ attrs, on }">
        <v-btn class="mr-n3" icon v-bind="attrs" v-on="on">
          <v-icon color="white">mdi-dots-vertical</v-icon>
        </v-btn>
      </template>
      <v-card>
        <v-card-title class="text-h5">
          Opciones
        </v-card-title>
        <v-card-text>
          <v-btn elevation="0" block text class="my-2 px-0 rounded-lg text-none">
            Informar como vendido
          </v-btn>

          <v-btn elevation="0" block text class="my-2 px-0 rounded-lg text-none">
            Compartir
          </v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-menu offset-y left v-if="!isMobile" v-model="dialog">
      <template v-slot:activator="{ attrs, on }">
        <v-btn icon v-bind="attrs" v-on="on">
          <v-icon color="white">mdi-dots-vertical</v-icon>
        </v-btn>
      </template>
      <v-list width="190" max-width="190">
        <v-list-item link>
          Informar como vendido
        </v-list-item>
        <v-list-item link>
          Compartir
        </v-list-item>
      </v-list>
    </v-menu>

  </div>
</template>

<script>
export default {
  props: {
    showDialog: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      dialog: this.showDialog
    }
  },
  computed: {
    isMobile () {
      return this.$vuetify.breakpoint.mobile && window.innerWidth < 800
    }
  },
  methods: {
    close () {
      this.$emit(false)
    }
  }
}
</script>

<style lang="scss" scoped>
  .v-btn {
    font-size: 16px;
  }
  .v-list-item{
    font-size: 14px;
    min-height: 35px;
    place-content: baseline;
  }
</style>
