<script>
export default {
  components: {
  },
  data () {
    return {
      drawer: false,
      search: '',
      searching: false,
      showSaleInformation: true,
      department: null,
      imageSelected: null,
      categorySelected: null,
      store: null,
      storeSlug: this.$route.params.slug,
      hash: null,
      categorySlugPath: this.$route.params.category_slug || null,
      productSlugPath: this.$route.params.product_slug || null,
      filter: {
        _id: null,
        params: {
          category: 'all'
        }
      },
      options: {
        duration: 750,
        offset: 150,
        easing: 'easeInOutCubic'
      },
      markPosition: false,
      loading1: false,
      products: this.$store.getters.products(),
      storeTemp: [],
      properties: {
        mainImageWidth: 450,
        mainImageHeight: 398,
        bottomBarHeight: '245px'
      }
    }
  },
  computed: {

    selectedImage: function () {
      return this.imageSelected
    },

    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': {
          this.properties.bottomBarHeight = '245px'
          this.properties.mainImageHeight = '275px'
          return true
        }
        case 'sm': {
          this.properties.bottomBarHeight = '315px'
          return (this.$vuetify.breakpoint.mobile || window.innerWidth < 600) || false
        }
        case 'md': {
          this.properties.bottomBarHeight = '382px'
          return false
        }
        case 'lg': {
          this.properties.bottomBarHeight = '382px'
          return false
        }
        case 'xl': {
          this.properties.bottomBarHeight = '382px'
          return false
        }
      }
    },
    isMobile () {
      if (window.innerWidth < 700 && this.$vuetify.breakpoint.mobile) {
        this.properties.mainImageHeight = '275px'
        return true
      }
      return false
    },

    innerWidth () {
      return window.innerWidth
    },

    updateWidth () {
      return window.innerWidth < 865
    },

    random () {
      var x = parseInt(Math.random() * 10)
      return x > 5 ? x - 5 : x
    }
  },
  methods: {
    searchProducts () {
      alert('Searching...' + this.search)
    },
    getImgUrl (image) {
      // return process.env.VUE_APP_API_IMAGE_URL + image
      var images = require.context('../../../assets/', false)
      return images('./' + image)
    }
  }
}
</script>

