<template>
  <div>
      <!--UPLOAD-->
      <form enctype="multipart/form-data" novalidate v-if="(isInitial || isSaving) && !isAdmin">
        <div :class="uploadedFilesSrc.length == 0 ? 'dropbox' : 'dropbox mb-7'" :style="error ? 'color: #b71c1c !important; outline: 2px dashed #b71c1c;' : ''">
          <input ref="input_file" id="files" type="file" :multiple="multiple" :name="uploadFieldName" :disabled="isSaving" @change="showImagePreview($event)"
            accept="image/*" title="" class="input-file">
            <p v-if="isInitial && uploadedFilesSrc.length == 0">
              Toque para buscar imgen
            </p>
            <p v-if="isSaving">
              Uploading {{ fileCount }} files...
            </p>
            <v-row dense class="mx-0 my-2 pa-4">
              <v-col align="center" align-self="center">
                <ImageCard v-for="(src, i) in uploadedFilesSrc" :key="i" :url="src"  @remove="removeImage(src)" />
              </v-col>
            </v-row>
        </div>
        <p v-if="isInitial && uploadedFilesSrc.length == 0"
          class="text-right text-bold"
          :style="error ? 'color: #b71c1c !important; font-size: 12px; margin-bottom: 0px !important;' : 'font-size: 12px'">
          Tamao mximo: 1Mb
        </p>
        <div class="v-text-field__details mb-3" v-if="error">
          <div class="v-messages theme--light error--text" role="alert">
            <div class="v-messages__wrapper">
              <div class="v-messages__message">
                Rellene este campo
              </div>
            </div>
          </div>
        </div>
      </form>
      <v-row dense class="mx-0 my-2 pa-4" v-if="isAdmin">
        <v-col align="center" align-self="center">
          <ImageCard v-for="(src, i) in uploadedFilesSrc" :key="i" :url="src" :removable="false" @remove="removeImage(src)" />
        </v-col>
      </v-row>

  </div>
</template>

<!-- Javascript -->
<script>
import ImageCard from './ImageCard'

const STATUS_INITIAL = 0; const STATUS_SAVING = 1; const STATUS_SUCCESS = 2; const STATUS_FAILED = 3

export default {
  name: 'FileUpload',
  components: {
    ImageCard
  },
  props: {
    images: {
      Type: Array,
      required: false
    },
    image: {
      Type: String,
      required: false
    },
    multiple: {
      type: Boolean,
      default: false
    },
    error: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      uploadedFiles: [],
      uploadedFilesSrc: [],
      uploadError: null,
      currentStatus: null,
      uploadFieldName: 'photos'
    }
  },
  created () {
    if (this.image != null) {
      this.uploadedFilesSrc = [process.env.VUE_APP_API_IMAGE_URL + this.image]
    }
    if (this.images != null && this.images.length > 0) {
      this.uploadedFilesSrc = process.env.VUE_APP_API_IMAGE_URL + this.images
    }
  },
  watch: {
    // uploadedFilesSrc () {
    //   return this.uploadedFilesSrc
    // }
  },
  computed: {
    isInitial () {
      return this.currentStatus === STATUS_INITIAL
    },
    isSaving () {
      return this.currentStatus === STATUS_SAVING
    },
    isSuccess () {
      return this.currentStatus === STATUS_SUCCESS
    },
    isFailed () {
      return this.currentStatus === STATUS_FAILED
    },
    isAdmin () {
      return this.$store.getters.isAdmin()
    }
  },
  methods: {
    reset () {
      // reset form to initial state
      this.currentStatus = STATUS_INITIAL
      this.uploadedFiles = []
      this.uploadError = null
    },

    removeImage (src) {
      const index = this.uploadedFilesSrc.indexOf(src)
      this.uploadedFilesSrc.splice(index, 1)
      this.uploadedFiles.splice(index, 1)
      this.$emit('imageAdded', null)
    },

    showImagePreview (event) {
      this.fileCount = event.target.files.length

      event.target.files.forEach(file => {
        if (this.uploadedFiles.indexOf(file) === -1) {
          if (((file.size / 1024) / 1024).toFixed(4) <= 1) {
            this.uploadedFiles.push(file)
            this.uploadedFilesSrc.push(URL.createObjectURL(file))

            const img = file
            const reader = new FileReader()
            reader.readAsDataURL(img)
            reader.onload = () => {
              this.$emit('imageAdded', reader.result)
            }
          } else {
            this.$toast.warning('La imagen supera el tamao admitido')
          }
        }
      })
    }
  },
  mounted () {
    this.reset()
  }
}
</script>

<!-- SASS styling -->
<style lang="scss">
.dropbox {
    outline: 2px dashed grey; /* the dash box */
    // outline-offset: -10px;
    // background: lightcyan;
    color: dimgray;
    // padding: 10px 10px;
    min-height: 200px; /* minimum height */
    position: relative;
    cursor: pointer;
    margin: 24px 0 12px;
  }

  .input-file {
    opacity: 0; /* invisible but it's there! */
    width: 100%;
    height: 200px;
    position: absolute;
    cursor: pointer;
  }

  // .dropbox:hover {
  //   background: lightblue; /* when mouse over to the drop zone, change color */
  // }

  .dropbox p {
    font-size: 1.2em;
    text-align: center;
    padding: 50px 0;
  }
</style>
