export const generalRules = {
  data () {
    return {
      card: v => {
        if (!isNaN(parseInt(v)) && v > 0 && v.length === 12) {
          return true
        }
        return 'Su tarjeta debe contener 12 dgitos'
      },
      required: value => !!value || 'Rellene este campo'
    }
  }
}
