<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar
          dark
          color="#ffa07a"
        >
          <v-btn
            icon
            dark
            @click="close"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Filtrar</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn
              dark
              text
              @click="saveFilter"
            >
              Guardar
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-list
          three-line
          subheader
        >
          <br>

          <v-list-item>
            <v-list-item-content>
              <v-list-item-title>Departamento</v-list-item-title>
              <v-list-item-subtitle></v-list-item-subtitle>
              <v-btn-toggle
                v-model="gender"
                multiple
                group
                class="justify-center"
              >
                <v-btn class="text-capitalize" value="Hombre" text outlined>
                  Hombre
                </v-btn>
                <v-btn class="text-capitalize" value="Mujer" text outlined>
                  Mujer
                </v-btn>
                <v-btn class="text-capitalize" value="Nio" text outlined>
                  Nio
                </v-btn>
                <v-btn class="text-capitalize" value="Nia" text outlined>
                  Nia
                </v-btn>
              </v-btn-toggle>
            </v-list-item-content>
          </v-list-item>

          <v-list-item>
            <v-list-item-content>
              <v-list-item-title>Categoras</v-list-item-title>
              <v-list-item-subtitle></v-list-item-subtitle>
              <v-chip-group v-model="selectedCategories" column multiple>
                <v-chip filter outlined v-for="(category, i) in categories" :key="i">{{category.name}}</v-chip>
              </v-chip-group>
            </v-list-item-content>
          </v-list-item>

          <v-list-item>
            <v-list-item-content>
              <v-list-item-title>Talla</v-list-item-title>
              <v-list-item-subtitle></v-list-item-subtitle>
              <v-btn-toggle v-model="size" multiple group class="justify-center">
                <v-btn value="S" text outlined>
                  S
                </v-btn>
                <v-btn value="M" text outlined>
                  M
                </v-btn>
                <v-btn value="L" text outlined>
                  L
                </v-btn>
                <v-btn value="XL" text outlined>
                  X
                </v-btn>
                <v-btn value="XXL" text outlined>
                  XXL
                </v-btn>
              </v-btn-toggle>
            </v-list-item-content>
          </v-list-item>

          <v-list-item>
            <v-list-item-content>
              <v-list-item-title>Color</v-list-item-title>
              <v-list-item-subtitle></v-list-item-subtitle>
              <v-color-picker
                dot-size="25"
                hide-canvas
                hide-inputs
                hide-mode-switch
                hide-sliders
                mode="hexa"
                show-swatches
                swatches-max-height="141"
                width="100%"
                style="max-width: 100%"
              ></v-color-picker>
            </v-list-item-content>
          </v-list-item>

        </v-list>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
export default {
  props: {
    showDialog: {
      type: Boolean,
      default: false
    },
    categories: {
      type: Array,
      require: false
    }
  },
  data () {
    return {
      dialog: this.showDialog,
      notifications: false,
      sound: true,
      size: true,
      gender: true,
      color: true,
      widgets: false,
      selectedCategories: []
    }
  },
  created () {
  },
  watch: {
    showDialog (newValue, oldValue) {
      this.dialog = newValue
    }
    // selectedCategories (oldValue, newValue) {
    //   var selected = newValue.filter(function (n) {
    //     return this.categories.indexOf(n) !== -1
    //   })
    //   if(selected.length === this.categories.length - 1 && selected.indexOf('Todas las categorias') !== -1){

    //   }
    // }
  },
  methods: {
    close () {
      this.$emit('close', false)
      this.dialog = false
    },
    saveFilter () {
      this.$emit('close', false)
      this.dialog = false
    }
  }
}
</script>
