extend('moreThanZero', {
  validate (value) {
    return value >= 1
  },
  message: 'Escoja un valor vlido.'
})

extend('positive', {
  validate (value) {
    return value >= 0
  },
  message: 'Escoja un valor vlido.'
})

extend('mayores', {
  validate (value) {
    return value >= 0
  },
  message: 'Introduzca un nmero de 12 dgitos.'
})

extend('required', {
  ...required,
  message: 'Rellene este campo'
})

extend('max', {
  ...max,
  message: 'Introduzca un nmero de no ms de {length} dgitos.'
})

extend('min', {
  ...min,
  message: 'El mnimo nmero de dgitos es {length}'
})

/// Date validators///

extend('afterToday', {
  validate (value) {
    const [day, month, year] = value.split('/')
    const date = new Date(`${month}/${day}/${year}`)
    const today = new Date()
    return date.getTime() >= today.getTime()
  },
  message: 'Escoja una fecha posterior a la actual.'
})

extend('afterCurrentTime', {
  validate (value) {
    const time = value.split(' ')[1]
    const today = new Date()
    const currentTime = today.getHours() + ':' + today.getMinutes()
    return time >= currentTime
    // return false
  },
  message: 'Escoja una fecha posterior a la actual.'
})

extend('after', {
  params: ['target'],
  validate (value, { target }) {
    var [day, month, year] = target.split('/')
    const from = new Date(`${month}/${day}/${year}`)

    var [day2, month2, year2] = value.split('/')
    const until = new Date(`${month2}/${day2}/${year2}`)
    return until.getTime() >= from.getTime()
  },
  message: 'Debe elegir una fecha posterior a la ida.'
})

// Password

extend('confirmPassword', {
  params: ['target'],
  validate (value, { target }) {
    return value === target
  },
  message: 'Las contraseas no coinciden'
})

extend('confirmed', {
  ...confirmed,
  message: 'Las contraseas no coinciden'
})

extend('email', {
  ...email,
  message: 'Introduzca un correo vlido'
})

extend('numeric', {
  ...numeric,
  message: 'Solo se aceptan nmero'
})

extend('alpha_spaces', {
  ...alpha_spaces,
  message: 'Solo se aceptan letras'
})

// Phone number

// extend('regex', {
//   ...regex,
//   message: 'Escriba un nmero de telfono vlido'
// })

extend('ci', {
  validate (value) {
    const ciRegex = /[0-9]{2}(?:0[0-9]|1[0-2])(?:0[1-9]|[12][0-9]|3[01])[0-9]{5}/
    if (value == null || ciRegex.test(value)) {
      return true
    }
  },
  message: 'Escriba un nmero de carnet vlido'
})

extend('phoneNumber', {
  validate (value) {
    const dateRegex = /^(\+?)\d+$/
    if (value == null || dateRegex.test(value)) {
      return true
    }
  },
  message: 'Escriba un nmero de telfono vlido'
})

extend('webSite', {
  validate (value) {
    if (value == null) {
      return true
    }
    const pattern = new RegExp('^(https?:\\/\\/)?' + // protocol
    '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|' + // domain name
    '((\\d{1,3}\\.){3}\\d{1,3}))' + // OR ip (v4) address
    '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
    '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
    '(\\#[-a-z\\d_]*)?$', 'i') // fragment locator
    return pattern.test(String(value))
  },
  message: 'Direccin de enlace no vlida'
})

