<template>
  <v-main>
    <v-app-bar
      absolute
      color="white"
      elevate-on-scroll
      scroll-target="#scrolling-techniques-7"
      class="header-toolbar"
      style="position: fixed; z-index: 8; width: 100%;"
    >
      <back-button />

      <v-toolbar-title>Nombre de la venta</v-toolbar-title>

    </v-app-bar>
    <v-container class="pt-11 px-0">
      <v-row class="pt-md-6 pt-sm-6 px-md-6 px-sm-6 pa-0 ma-0" justify="space-around">

        <v-col :class="isMobile ? 'pa-0' : 'px-5'" cols="12" md="6" :sm="updateWidth ? '12' : '6'">
          <v-row class="ma-0">
            <v-img contain max-height="398" height="min-content" :width="properties.mainImageWidth" class="blog-box-shadow"
              :src="getImgUrl('casa-en-venta.png')" :lazy-src="getImgUrl('casa-en-venta.png')">
            </v-img>
          </v-row>
        </v-col>

        <v-col cols="12" md="6" :sm="updateWidth ? '12' : '6'" xs="12" class="">
          <p id="title" class="mb-2 px-md-6 px-sm-6">Nombre de la venta</p>
          <v-row>
            <rating :rated="userHasRate" @ratingDone="addNewRate($event)"></rating>
            <v-btn class="ml-3 mt-n1" icon :color="toNotify(store) ? 'yellow darken-4' : 'grey lighten-1'" @click="addNewProductNotification(store)">
              <v-icon>{{toNotify(store) ? 'mdi-bell' : 'mdi-bell-ring'}}</v-icon>
            </v-btn>
          </v-row>
          <StoreInformation v-if="!breakpoint && showSaleInformation" :updateWidth="updateWidth" />
        </v-col>

      </v-row>

      <div v-if="breakpoint">
        <v-tabs
          v-model="tab"
          centered
          icons-and-text
          height="37"
        >
          <v-tabs-slider></v-tabs-slider>

          <v-tab href="#store-articles">
            Articulos
          </v-tab>

          <v-tab href="#store-information">
            Informacin
          </v-tab>

        </v-tabs>

        <v-tabs-items v-model="tab">
          <v-tab-item value="store-articles">
            <ProductsList :store="store" />
          </v-tab-item>
          <v-tab-item value="store-information">
            <StoreInformation v-if="showSaleInformation" :updateWidth="updateWidth" />
          </v-tab-item>
        </v-tabs-items>
      </div>

      <div v-if="!breakpoint">
          <v-divider class="my-7 mx-15"></v-divider>
          <ProductsList :store="store" />
      </div>
    </v-container>
  </v-main>
</template>

<script>
import ProductsList from '@/components/Store/ProductsList'
import StoreInformation from './StoreInformation'
import saleServices from '@/axios/store.services'
import Rating from '../../../components/Rating/Rating.vue'
import BackButton from '@/components/BackButton'

export default {
  name: 'Sale',
  components: {
    StoreInformation,
    ProductsList,
    Rating,
    BackButton
  },
  data () {
    return {
      drawer: false,
      search: '',
      searching: false,
      showSaleInformation: true,
      tab: null,
      department: null,
      imageSelected: null,
      categorySelected: null,
      store: null,
      storeSlug: this.$route.params.slug,
      hash: null,
      categorySlugPath: this.$route.params.category_slug || null,
      productSlugPath: this.$route.params.product_slug || null,
      filter: {
        _id: null,
        params: {
          category: 'all'
        }
      },
      options: {
        duration: 750,
        offset: 150,
        easing: 'easeInOutCubic'
      },
      markPosition: false,
      loading1: false,
      loading2: false,
      product: {
        images: [1, 2, 3, 4, 5]
      },
      products: this.$store.getters.products(),
      storeTemp: [],
      // selectedItem: 1,
      categories: [
        { name: 'Todas las categorias', icon: 'mdi-account', _id: 0, slug: 'todas-las-categorias' },
        { name: 'Accesorios', icon: 'mdi-account', _id: 1, slug: 'accesorios' },
        { name: 'Artesania', icon: 'mdi-account', _id: 2, slug: 'artesania' },
        { name: 'Decoracion', icon: 'mdi-flag', _id: 3, slug: 'decoracion' },
        { name: 'Del Hogar', icon: 'mdi-flag', _id: 4, slug: 'del-hogar' },
        { name: 'Ropa', icon: 'mdi-clock', _id: 5, slug: 'ropa' },
        { name: 'Tecnologia', icon: 'mdi-flag', _id: 6, slug: 'tecnologia' }
      ],
      properties: {
        mainImageWidth: 450,
        mainImageHeight: 398,
        bottomBarHeight: '245px'
      }
    }
  },
  watch: {

  },
  created () {
    this.loading1 = true

    if (this.categorySlugPath) {
      // getting the category id
      this.categorySlugPath = this.categories.find(cat => cat.slug === this.categorySlugPath)
      this.categorySlugPath = this.categorySlugPath ? this.categorySlugPath._id : null
      this.filter.params.category = this.categorySlugPath || null
      if (this.filter.params.category === null && this.categories.length > 0) {
        this.filter.params.category = this.categories[0]._id
      }
      this.categorySelected = this.filter.params.category
    }

    // loading store information
    saleServices.getStores({ params: { slug: this.storeSlug } }).then((result) => {
      this.store = result.data[0]
      this.loading1 = false
      this.store = {
        id: 2,
        toNotify: false
      }
    }).catch((err) => {
      console.log(err)
      // this.$toast.error('Error al cargar venta')
    })

    this.store = {
      id: 2,
      toNotify: false
    }
  },
  computed: {

    selectedImage: function () {
      return this.imageSelected
    },

    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': {
          this.properties.bottomBarHeight = '245px'
          this.properties.mainImageHeight = '222px'
          return true
        }
        case 'sm': {
          this.properties.bottomBarHeight = '315px'
          return (this.$vuetify.breakpoint.mobile || window.innerWidth < 600) || false
        }
        case 'md': {
          this.properties.bottomBarHeight = '382px'
          return false
        }
        case 'lg': {
          this.properties.bottomBarHeight = '382px'
          return false
        }
        case 'xl': {
          this.properties.bottomBarHeight = '382px'
          return false
        }
      }
    },
    isMobile () {
      if (window.innerWidth < 700 && this.$vuetify.breakpoint.mobile) {
        this.properties.mainImageHeight = '275px'
        return true
      }
      return false
    },

    innerWidth () {
      return window.innerWidth
    },

    updateWidth () {
      return window.innerWidth < 865
    },

    random () {
      var x = parseInt(Math.random() * 10)
      return x > 5 ? x - 5 : x
    },

    userHasRate () {
      return localStorage.getItem('storeRating') != null
    }
  },
  methods: {
    searchProducts () {
      alert('Searching...' + this.search)
    },
    getImgUrl (image) {
      // return process.env.VUE_APP_API_IMAGE_URL + image
      var images = require.context('../../../assets/', false)
      return images('./' + image)
    },

    addNewProductNotification (event) {
      if (!event.toNotify) {
        this.$store.dispatch('addNewProductNotification', event)
      } else {
        this.$store.dispatch('removeNewProductNotification', event)
      }
      event.toNotify = !event.toNotify
    },

    toNotify (event) {
      return this.$store.getters.existNewProductNotification(event.id)
    },

    addNewRate (event) {
      if (localStorage.getItem('storeRating')) {
        const ratings = JSON.parse(localStorage.getItem('storeRating'))
        ratings.push(event)
        localStorage.setItem('storeRating', JSON.stringify(ratings))
      } else {
        localStorage.setItem('storeRating', JSON.stringify([event]))
      }
    }
  }
}
</script>

