import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/store-module/home'
import Sale from '../views/store-module/sale'
import ItemInformation from '../views/store-module/sale/items/ItemInformation.vue'
import Profile from '../views/settings/profile/index.vue'
import Calendar from '../views/settings/calendar/'
import SalePromotionForm from '../views/settings/calendar/SalePromotionForm.vue'
import MyStore from '../views/settings/store/'
import CreateStore from '../views/settings/store/CreateStore.vue'
import SaleProductsAdministration from '../views/settings/store/products/SaleProductsAdministration.vue'
import ProductForm from '../views/settings/store/products/ProductForm.vue'
import Login from '../views/authentication/Login.vue'
import Signup from '../views/authentication/Signup.vue'
import Confirmation from '../views/authentication/Confirmation.vue'
import ForgotPassword from '../views/authentication/ForgotPassword.vue'
import Error404 from '../views/404/404.vue'
import Store from '@/views/store'
import Schedule from '@/views/schedule'

Vue.use(VueRouter)

const isAdmin = (to, from, next) => {
  // if (localStorage.getItem('store_user') && JSON.parse(localStorage.getItem('store_user')).user.role && JSON.parse(localStorage.getItem('store_user')).user.role.name === 'Admin') {
  //   router.replace('/administracion')
  // } else {
  next()
  // }
}

