//   localStorage.clear()
//   if (from.name !== 'stores') {
//     router.replace('/stores')
//   } else {
//     window.location.reload()
//   }
// }

const routes = [
  // {
  //   path: '/', redirect: 'stores', beforeEnter: isAdmin
  // },
  {
    path: '/', name: 'home', component: Home, beforeEnter: isAdmin
  },
  {
    path: '/login', name: 'login', component: Login
  },
  {
    path: '/registrarse', name: 'signup', component: Signup
  },
  {
    path: '/confirmar', name: 'confirmation', component: Confirmation
  },
  {
    path: '/recuperar-clave', name: 'forgotPassword', component: ForgotPassword
  },
  // {
  //   path: '/logout', name: 'logout', component: RestaurantHome, beforeEnter: loguot
  // },
  {
    path: '/error/404', name: '404', component: Error404
  },
  // {
  //   path: '/stores', name: 'stores', component: RestaurantHome, beforeEnter: isAdmin
  // },
  {
    path: '/stores', name: 'stores', component: Store
  },
  {
    path: '/calendar', name: 'calendar', component: Schedule
  },
  {
    path: '/ventas', name: 'sales', component: Sale, beforeEnter: isAdmin
  },
  {
    path: '/venta/:slug', name: 'sale-detail', component: Sale
  },
  {
    path: '/venta/:slug/:category_slug', name: 'sale-category-detail', component: Sale, beforeEnter: isAdmin
  },
  {
    path: '/venta/:slug/:category_slug/:product_slug', name: 'sale-category-product-detail', component: ItemInformation, beforeEnter: isAdmin
  },
  { path: '/administration/profile', name: 'profile', component: Profile },
  { path: '/administration/my-store', name: 'my-store', component: MyStore },
  { path: '/administration/my-store/create', name: 'create-store', component: CreateStore },
  { path: '/administration/my-store/products', name: 'store-products', component: SaleProductsAdministration },
  { path: '/administration/my-store/create-products', name: 'create-product', component: ProductForm },
  { path: '/administration/my-calendar', name: 'my-calendar', component: Calendar },
  { path: '/administration/my-calendar/sale-promotion', name: 'sale-promotion', component: SalePromotionForm }

]

const scrollBehavior = (to, from, savedPosition) => {
  if (savedPosition) {
    return savedPosition
  }
  if (to.hash) {
    return { selector: to.hash }
  }
  return { x: 0, y: 0 }
}
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior
})

export default router
