    <v-main id="home" class="mt-14">

        <FilterDialog :show-dialog="filterDialog" />

        <Carrousel/>

        <!-- <v-row>
          <v-col cols="4" md="4" sm="6">
            <v-btn outlined text class="text-capitalize rounded-lg" @click="filterDialog = true">Filtrar</v-btn>
          </v-col>
          <v-col cols="8" md="4" sm="6">
              <v-text-field
