        <desktop-view v-if="!isMobile" :breakpoint="breakpoint"></desktop-view>
        <mobile-view v-if="isMobile" :breakpoint="breakpoint"></mobile-view>

    </v-main>
</template>

<script>
import Carrousel from './Carrousel'

import provinces from '@/nomencladores/provinces.json'
import { dateRules } from '@/mixins/date_rules'
import { numberRules } from '@/mixins/number_rules'
import { generalRules } from '@/mixins/general_rules'
import { apiServices } from '@/mixins/api.services'
import FilterDialog from '../../../components/FilterDialog.vue'
// import storeServices from '@/axios/store.services'

import MobileView from './mobile-view.vue'
import DesktopView from './desktop-view.vue'

export default {
  name: 'Home',
  components: {
    Carrousel,
    FilterDialog,
    MobileView,
    DesktopView
  },
  mixins: [numberRules, dateRules, generalRules, apiServices],
  data () {
    return {
      i: 0,
      dialog: true,
      filterDialog: false,
      isActive: false,
      search: {
        params: {
          general: null,
          pricesRange: null,
          delivery: null,
          foodType: null,
          province: null,
          deliveryMunicipalities: null
        }
      },
      pagination: {
        limit: 10,
        skip: 0,
        total: null
      },
      provinces: provinces,
      stores: this.$store.getters.products(),
      storeTemp: [],
      items: [
        {
          action: 'mdi-ticket',
          items: [{ title: 'List Item' }],
          title: 'Attractions'
        },
        {
          action: 'mdi-silverware-fork-knife',
          active: true,
          items: [
            { title: 'Breakfast & brunch' },
            { title: 'New American' },
            { title: 'Sushi' }
          ],
          title: 'Dining'
        },
        {
          action: 'mdi-school',
          items: [{ title: 'List Item' }],
          title: 'Education'
        },
        {
          action: 'mdi-run',
          items: [{ title: 'List Item' }],
          title: 'Family'
        },
        {
          action: 'mdi-bottle-tonic-plus',
          items: [{ title: 'List Item' }],
          title: 'Health'
        },
        {
          action: 'mdi-content-cut',
          items: [{ title: 'List Item' }],
          title: 'Office'
        },
        {
          action: 'mdi-tag',
          items: [{ title: 'List Item' }],
          title: 'Promotions'
        }
      ],
      properties: {
        bottomBarHeight: '245px',
        topItemHeight: 250,
        topItemWidth: 383,
        topItemMargin: 'ma-4'
      }
    }
  },
  mounted () {
    if (localStorage.getItem('store_search')) {
      this.search = JSON.parse(localStorage.getItem('store_search'))
      // this.searchForRestaurants()
      this.dialog = false
    }
  },
  computed: {
    municipalities: function () {
      if (this.search.params.province) {
        return this.provinces.find(p => p.name === this.search.params.province).municipalities
      }
      return []
    },
    innerWidth () {
      return window.innerWidth
    },
    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': {
          this.properties.bottomBarHeight = '245px'
          this.properties.topItemHeight = 200
          this.properties.topItemWidth = 272
          this.properties.topItemMargin = 'ma-2'
          return true
        }
        case 'sm': {
          this.properties.bottomBarHeight = '315px'
          return (this.$vuetify.breakpoint.mobile || window.innerWidth < 600) || false
        }
        case 'md': {
          this.properties.bottomBarHeight = '382px'
          return false
        }
        case 'lg': {
          this.properties.bottomBarHeight = '382px'
          return false
        }
        case 'xl': {
          this.properties.bottomBarHeight = '382px'
          return false
        }
      }
    },
    isMobile () {
      // return window.innerWidth < 700 && this.$vuetify.breakpoint.mobile;
      return this.$vuetify.breakpoint.mobile
    }
  },

  methods: {
    searchForRestaurants () {
      // this.stores = []
      // this.loading = true
      // this.dialog = false
      // storeServices.getStores(this.search).then((result) => {
      //   this.stores = result.data
      //   this.loading = false
      //   localStorage.setItem('cubamax_search', JSON.stringify(this.search))

      //   this.storesTemp = this.stores.slice(this.pagination.skip, this.pagination.limit)
      //   this.pagination.skip = this.pagination.skip + this.pagination.limit
      //   this.pagination.total = this.stores.length
      // }).catch(() => {
      //   this.dialog = false
      //   // this.$toast.error('Error al cargar los stores')
      // })
    }
  }
}
</script>

