<template>
  <v-main>
      <v-app-bar
        absolute
        color="white"
        elevate-on-scroll
        class="header-toolbar"
        style="position: fixed; z-index: 8; width: 100%;"
      >
        <back-button />

        <v-toolbar-title>{{ formTitle }}</v-toolbar-title>

        <v-spacer></v-spacer>

        <v-btn text @click="save">Guardar</v-btn>
      </v-app-bar>
      <v-container class="mt-16 turn-editedItem-form">

          <ValidationObserver ref="observer">
            <form autocomplete="off" @submit.prevent="updateProfile">
              <FileUpload :image="editedItem.image != 'no_image.jpeg' ? editedItem.image : null" @imageAdded="imageAdded($event)"/>

              <v-row justify="space-between">
                <v-col cols="12" class="pb-1">
                    <v-dialog
                      ref="dialog"
                      v-model="modal"
                      :return-value.sync="editedItem.date"
                      persistent
                      width="290px"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <ValidationProvider
                          v-slot="{ errors }"
                          name="date"
                          rules="required"
                        >
                          <v-text-field
                            ref="date"
                            id="date"
                            v-model="dateFormatted"
                            label="Seleccionar fecha"
                            prepend-inner-icon="mdi-calendar"
                            readonly
                            outlined
                            v-bind="attrs"
                            v-on="on"
                            @blur="editedItem.date = parseDate(dateFormatted)"
                            :error-messages="errors"
                            :hide-details="errors.length > 0 ? false : true"
                          ></v-text-field>
                        </ValidationProvider>
                      </template>
                      <v-date-picker
                        v-model="editedItem.date"
                        scrollable
                      >
                        <v-spacer></v-spacer>
                        <v-btn
                          text
                          color="primary"
                          @click="modal = false"
                        >
                          Cancel
                        </v-btn>
                        <v-btn
                          text
                          color="primary"
                          @click="$refs.dialog.save(editedItem.date)"
                        >
                          OK
                        </v-btn>
                      </v-date-picker>
                    </v-dialog>
                </v-col>

                <v-col cols="6" md="6" sm="6" xs="6" class="pb-1">
                  <ValidationProvider
                    v-slot="{ errors }"
                    name="start_hour"
                  >
                    <v-text-field
                      ref="start_hour"
                      id="start_hour"
                      v-model="editedItem.start"
                      type="time"
                      label="Inicio"
                      color="primary"
                      persistent-hint
                      class="mx-auto rounded-lg"
                      :error-messages="errors"
                      :hide-details="errors.length > 0 ? false : true"
                      dense
                      outlined
                    ></v-text-field>
                  </ValidationProvider>
                </v-col>
                <v-col cols="6" md="6" sm="6" xs="6" class="pb-1">
                  <ValidationProvider
                    v-slot="{ errors }"
                    name="end_hour"
                  >
                    <v-text-field
                      ref="end_hour"
                      id="end_hour"
                      v-model="editedItem.end"
                      type="time"
                      label="Fin"
                      color="primary"
                      persistent-hint
                      class="mx-auto rounded-lg"
                      :error-messages="errors"
                      :hide-details="errors.length > 0 ? false : true"
                      dense
                      outlined
                    ></v-text-field>
                  </ValidationProvider>
                </v-col>

                <v-col cols="12" class="pb-0">
                  <v-radio-group v-model="editedItem.sameAddress" row>
                    <v-radio class="mx-2 mt-0" label="Misma direccin" :value="true"></v-radio>
                    <v-radio class="mx-2 mt-0" label="Otra direccin" :value="false"></v-radio>
                  </v-radio-group>
                </v-col>

                <v-row v-if="!editedItem.sameAddress">
                  <v-col  cols="12" md="6" sm="6" xs="12" class="pb-1">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="province"
                      rules="required"
                    >
                      <!-- <label for="province">Provincia*</label> -->
                      <v-select
                        ref="province"
                        id="province"
                        v-model="editedItem.address.province"
                        :items="provinces"
                        item-text="name"
                        item-value="name"
                        label="Provincia *"
                        placeholder="Selecionar provincia"
                        outlined
                        dense
                        :readonly="isAdmin"
                        class="mx-auto rounded-lg"
                        no-data-text="No hay datos"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-select>
                    </ValidationProvider>
                  </v-col>

                  <v-col  cols="12" md="6" sm="6" xs="12" class="pb-1">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="municipality"
                      rules="required"
                    >
                      <!-- <label for="municipality">Municipio*</label> -->
                      <v-select
                        ref="municipality"
                        id="municipality"
                        v-model="editedItem.address.municipality"
                        :items="municipalities"
                        label="Municipio *"
                        placeholder="Seleccionar municipio"
                        outlined
                        dense
                        :readonly="isAdmin"
                        class="mx-auto rounded-lg"
                        no-data-text="No hay datos"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-select>
                    </ValidationProvider>
                  </v-col>

                  <v-col cols="12" md="12" sm="12" xs="12" class="pb-1">
                    <ValidationProvider
                        v-slot="{ errors }"
                        name="mainStreet"
                        rules="required"
                      >
                    <v-text-field
                      id="mainStreet"
                      v-model="editedItem.address.mainStreet"
                      label="Direccin"
                      placeholder="Direccion de la venta"
                      outlined
                      dense
                    :readonly="isAdmin"
                      class="mx-auto rounded-lg"
                      :error-messages="errors"
                      :hide-details="errors.length > 0 ? false : true"
                    ></v-text-field>
                  </ValidationProvider>
                  </v-col>

                  <v-col cols="12" class="pb-1">
                    <v-btn block color="primary" outlined style="text-transform: none; letter-spacing: initial;">
                      <span class="mr-2">Ubicar en el mapa</span>
                      <v-icon size="20">mdi-map-marker</v-icon>
                    </v-btn>
                  </v-col>
                </v-row>

                <v-col cols="12">
                  <!-- <label for="description">Descripcin</label> -->
                  <v-textarea
                    ref="description"
                    id="description"
                    name="description"
                    v-model="editedItem.description"
                    label="Descripcin"
                    placeholder="Breve descripcion de la venta"
                    outlined
                    :readonly="isAdmin"
                    hide-details
                    class="rounded-lg"
                  ></v-textarea>
                </v-col>

              </v-row>

            </form>
          </ValidationObserver>
      </v-container>
  </v-main>
</template>

<script>
// import userServices from '@/axios/user.services'
import { dateRules } from '@/mixins/date_rules'
import { numberRules } from '@/mixins/number_rules'
import { generalRules } from '@/mixins/general_rules'
import provinces from '@/nomencladores/provinces.json'
// import GoogleMapsMarker from '@/components/GoogleMapsMarker/GoogleMapsMarker.vue'
import FileUpload from '@/components/image-upload/FileUpload'
import BackButton from '@/components/BackButton.vue'
// import moment from 'moment'

export default {
  name: 'SalePromotionForm',
  mixins: [numberRules, dateRules, generalRules],
  components: {
    BackButton,
    // GoogleMapsMarker,
    FileUpload
  },
  data: function () {
    return {
      dateFormatted: null,
      menu: false,
      modal: false,
      menu2: false,
      search: '',
      selected: [],
      address: '',
      provinces: provinces,
      editMode: false,
      dialog: false,
      dialogDelete: false,
      markPosition: false,
      invalidCoordenates: false,
      date: null,
      date_menu: null,
      editedIndex: -1,
      editedItem: {
        storeId: null,
        image: null,
        date: null,
        start: null,
        end: null,
        description: null,
        sameAddress: true,
        address: {
          mainStreet: null,
          municipality: null,
          province: null,
          coordenates: null
        }
      },
      defaultItem: {
        storeId: null,
        image: null,
        date: null,
        start: null,
        end: null,
        description: null,
        sameAddress: true,
        address: {
          mainStreet: null,
          municipality: null,
          province: null,
          coordenates: null
        }
      }
    }
  },
  created () {
    if (this.$route.params.event) {
      this.editedItem = this.$route.params.event
      this.editedItem.start = this.editedItem.start.split(' ')[1]
      this.editedItem.end = this.editedItem.end.split(' ')[1]
      this.editMode = true
    }
  },
  mounted () {

  },
  watch: {
    'editedItem.date' (val) {
      this.dateFormatted = this.formatDate(this.editedItem.date)
    }
  },
  computed: {
    municipalities: function () {
      if (this.editedItem.address.province) {
        return this.provinces.find(p => p.name === this.editedItem.address.province).municipalities
      }
      return []
    },

    formTitle () {
      return this.editedItem.id ? 'Editar promocin' : 'Nueva promocin'
    },

    isAdmin () {
      return this.$store.getters.isAdmin()
    },

    coordenates () {
      return this.editedItem.address !== null ? this.editedItem.address.coordenates : ''
    }
  },
  methods: {
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    save () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          this.editedItem.start = `${this.editedItem.date} ${this.editedItem.start}`
          this.editedItem.end = `${this.editedItem.date} ${this.editedItem.end}`
          if (!this.editedItem.id) {
            this.$store.dispatch('addEvent', this.editedItem)
          }
          this.$router.back()
        }
      })
    },
    cancel () {
      this.$emit('cancel')
    },
    imageAdded (event) {
      this.editedItem.oldImage = this.editedItem.image
      this.editedItem.image = event
      this.emptyImageError = this.editedItem.image === null
    }
  }
}
</script>

<style lang="scss" scoped>
.row{
  margin: 7px 0 !important;
}
h3{
  text-transform: uppercase;
}
</style>
