<template>
  <v-main>
      <v-container class="fill-height">
        <v-row class="fill-height" align="center">
          <v-col id="signup-form" class="ma-auto" lg="4" md="5" sm="7" xs="7">
            <v-card class="rounded-xl">
              <v-col align-self="center">
                <h1 class="text-bolder text-center">REGSTRATE</h1>
                <ValidationObserver ref="observer">
                  <form autocomplete="off">

                    <!-- Nombre y apellidos -->
                        <ValidationProvider
                          v-slot="{ errors }"
                          name="firstname"
                          rules="required|alpha_spaces"
                        >
                          <!-- <label class="text-left" for="firstname">Nombre</label> -->
                          <v-text-field
                            ref="firstname"
                            id="firstname"
                            v-model="user.firstname"
                            label="Nombre"
                            outlined
                            dense
                            class="mx-auto my-3 rounded-lg"
                            :error-messages="errors"
                            :hide-details="errors.length > 0 ? false : true"
                          >
                          </v-text-field>
                        </ValidationProvider>

                        <ValidationProvider
                          v-slot="{ errors }"
                          name="lastname"
                          rules="required|alpha_spaces"
                        >
                          <!-- <label class="text-left" for="lastname">Apellidos</label> -->
                          <v-text-field
                            ref="lastname"
                            id="lastname"
                            v-model="user.lastname"
                            label="Apellidos"
                            outlined
                            dense
                            class="mx-auto my-3 rounded-lg"
                            :error-messages="errors"
                            :hide-details="errors.length > 0 ? false : true"
                          >
                          </v-text-field>
                        </ValidationProvider>

                    <!-- Correo y telefono -->
                        <!-- <ValidationProvider
                          v-slot="{ errors }"
                          name="username"
                          rules="required"
                        >
                          <v-text-field
                            ref="username"
                            id="username"
                            v-model="user.username"
                            label="Alias"
                            outlined
                            dense
                            class="mx-auto my-3 rounded-lg"
                            :error-messages="errors"
                            :hide-details="errors.length > 0 ? false : true"
                          >
                          </v-text-field>
                        </ValidationProvider> -->

                        <ValidationProvider
                          v-slot="{ errors }"
                          rules="required|email"
                        >
                          <!-- <label for="email">Correo electrnico</label> -->
                          <v-text-field
                            ref="email"
                            id="email"
                            v-model="user.email"
                            label="Correo electrnico"
                            placeholder="Escriba su correo"
                            outlined
                            dense
                            class="mx-auto my-3 rounded-lg"
                            :error-messages="errors"
                            :hide-details="errors.length > 0 ? false : true"
                          >
                          </v-text-field>
                        </ValidationProvider>

                    <!-- Contraseas -->
                        <ValidationProvider
                          v-slot="{ errors }"
                          name="password"
                          rules="required|min:8"
                        >
                          <!-- <label class="text-left" for="password">Contrasea</label> -->
                          <v-text-field
                            ref="password"
                            id="password"
                            v-model="user.password"
                            label="Contrasea"
                            placeholder="Mnimo 8 caracteres"
                            outlined
                            dense
                            :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                            :type="show1 ? 'text' : 'password'"
                            class="mx-auto my-3 rounded-lg"
                            :error-messages="errors"
                            :hide-details="errors.length > 0 ? false : true"
                            @click:append="show1 = !show1"
                          >
                          </v-text-field>
                        </ValidationProvider>

                        <ValidationProvider
                          v-slot="{ errors }"
                          name="passwordConfirmation"
                          rules="required|min:8|confirmed:password"
                        >
                          <!-- <label class="text-left" for="passwordConfirmation">Confirmar su contrasea</label> -->
                          <v-text-field
                            ref="passwordConfirmation"
                            id="passwordConfirmation"
                            v-model="passwordConfirmation"
                            label="Confirmar su contrasea"
                            placeholder="Repita todos los caracteres"
                            outlined
                            dense
                            :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                            :type="show2 ? 'text' : 'password'"
                            class="mx-auto my-3 rounded-lg"
                            :error-messages="errors"
                            :hide-details="errors.length > 0 ? false : true"
                            @click:append="show2 = !show2"
                          >
                          </v-text-field>
                        </ValidationProvider>

                    <v-row align="center" justify="space-around">
                      <v-col align="center">
                        <v-btn class="rounded-lg text-bold shadow" color="secondary" @click="signup">REGISTRARME</v-btn>
                      </v-col>
                    </v-row>

                    <v-row align="center" justify="space-around">
                      <v-col align="center">
                        <p>Si ya tienes una cuenta, <router-link class="text-info" to="/iniciar">Inicia sesin</router-link></p>
                      </v-col>
                    </v-row>

                  </form>
                </ValidationObserver>
              </v-col>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
  </v-main>
</template>

<script>
import authenticationServices from '@/axios/authentication.services'
import router from '../../router'

export default {
  name: 'Signup',
  data () {
    return {
      show1: false,
      show2: false,
      user: {
        firstname: null,
        lastname: null,
        // phone: null,
        email: null,
        password: null
        // profile: {
        //   address: {
        //     houseNumber: null,
        //     streetDirection: null,
        //     streetName: null,
        //     streetSuffix: null,
        //     city: null,
        //     state: null,
        //     zip: null
        //   },
        //   housePhone: null,
        //   dateBirth: null,
        //   gender: null,
        //   notifications: null,
        //   telegram: {
        //     username: null,
        //     chatId: null
        //   }
        // },
        // isActive: false,
        // dateJoined: null,
        // resetToken: null,
        // stores: [],
        // requests: [],
        // contacts: [],
        // wishList: null,
        // role: null
      },
      passwordConfirmation: null
    }
  },
  created () {
    // var storedUser = localStorage.getItem('store_user')
    // if (storedUser) {
    //   storedUser = JSON.parse(storedUser)
    //   this.user.email = storedUser.email
    // }
  },
  methods: {
    signup () {
      this.$refs.observer.validate().then((result) => {
        if (result && this.passwordConfirmation !== null) {
          authenticationServices.signUp(this.user).then((result) => {
            if (result) {
              this.$toast.success('Usted se ha registrado con xito!')
              this.$toast.success('Se ha enviado un cdigo de confirmacin a su correo!')
              router.push({ name: 'confirmation', params: { sendEmail: false, user: { email: this.user.email, password: this.user.password } } })
            }
          }).catch((err) => {
            if (err.response.data.name === 'ValidatorError' && err.response.data.details.field === 'email') {
              this.$toast.error('El correo no es vlido o ya existe!')
              router.replace('/iniciar')
            } else if (err.response.data.name === 'NodeMailerError') {
              this.$toast.error('Error al enviar cdigo de confirmacin!')
              router.replace('/iniciar')
            } else {
              this.$toast.error('Error al registrar el usuario!')
              router.replace('/iniciar')
            }
          })
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.v-main{
  background: lightsalmon;
  // background-image: url('../../assets/login/Background.png') !important;
  // background-size: cover;
  /* height: 85%; */
}

#signup-form{
  // position: absolute;
  // left: 50%;
  // top: 50%;
  // transform: translate(-50%, -50%);
  // -webkit-transform: translate(-50%, -50%);
}

.row{
  margin: 0
}
</style>
