const state = {
  notifications: [],
  newProductNotifications: []
}

const mutations = {

  addNotification (state, event) {
    if (state.notifications.filter(item => item.id === event.id).length === 0) {
      state.notifications.push(event)
    }
  },
  removeNotification (state, event) {
    state.notifications = state.notifications.filter(item => item.id !== event.id)
  },

  addNewProductNotification (state, event) {
    if (state.newProductNotifications.filter(item => item.id === event.id).length === 0) {
      state.newProductNotifications.push(event)
    }
  },
  removeNewProductNotification (state, event) {
    state.newProductNotifications = state.newProductNotifications.filter(item => item.id !== event.id)
  }
}

const actions = {

  addNotification ({ commit }, event) {
    commit('addNotification', event)
  },

  removeNotification ({ commit }, event) {
    commit('removeNotification', event)
  },

  addNewProductNotification ({ commit }, event) {
    commit('addNewProductNotification', event)
  },

  removeNewProductNotification ({ commit }, event) {
    commit('removeNewProductNotification', event)
  }

}

const getters = {
  notifications: (state) => () => {
    return state.notifications
  },
  getNotification: (state) => (index) => {
    return state.notifications[index]
  },
  existNotification: (state) => (id) => {
    return state.notifications.filter(item => item.id === id).length > 0
  },
  existNewProductNotification: (state) => (id) => {
    return state.newProductNotifications.filter(item => item.id === id).length > 0
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
