import axiosCustom from '../axios'

export default class AuthenticationServices {
  static login (data) {
    return axiosCustom.post('/auth/login', data)
  }

  static signUp (data) {
    return axiosCustom.post('/auth/signup', data)
  }

  static reSendEmail (data) {
    return axiosCustom.post('/auth/resend-email', data)
  }

  static verifyEmail (data) {
    return axiosCustom.post('/auth/verify-code', data)
  }

  static forgotPassword (data) {
    return axiosCustom.post('/auth/forgotPassword', data)
  }

  static banUser (data) {
    return axiosCustom.patch(`/admin/user/${data.userId}/ban-user?value=${data.value}`)
  }
}
