<template>
  <v-main>
      <v-app-bar
        absolute
        color="white"
        elevate-on-scroll
        class="header-toolbar"
        style="position: fixed; z-index: 8; width: 100%;"
      >
        <back-button />

        <v-toolbar-title>{{ formTitle }}</v-toolbar-title>

        <v-spacer></v-spacer>

        <v-btn text @click="save">Guardar</v-btn>
      </v-app-bar>
    <v-container class="mt-16">
      <ValidationObserver ref="observer">
        <form autocomplete="off">
          <FileUpload :error="emptyImageError" :image="editedItem.photo != 'no_image.jpeg' ? editedItem.photo : null" @imageAdded="imageAdded($event)"/>

          <!-- Nombre y telefono -->
          <v-row justify="space-between">
            <v-col  cols="12" md="6" sm="6" xs="12">
              <ValidationProvider
                v-slot="{ errors }"
                name="name"
                rules="required"
              >
                <!-- <label for="name">Nombre*</label> -->
                <v-text-field
                  ref="name"
                  id="name"
                  v-model="editedItem.name"
                  label="Nombre *"
                  placeholder="Nombre de la tienda"
                  outlined
                  dense
                  class="mx-auto rounded-lg"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                >
                </v-text-field>
              </ValidationProvider>
            </v-col>
            <v-col  cols="12" md="6" sm="6" xs="12">
              <ValidationProvider
                v-slot="{ errors }"
                name="phone"
                rules="required|min:8|numeric"
              >
                <!-- <label for="phone">Telfono*</label> -->
                <v-text-field
                  ref="phone"
                  id="phone"
                  v-model="editedItem.phone"
                  label="Telfono *"
                  placeholder="Telfono del local"
                  outlined
                  dense
                  class="mx-auto rounded-lg"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                >
                </v-text-field>
              </ValidationProvider>
            </v-col>

          <!-- Correo y Web -->
            <v-col  cols="12" md="6" sm="6" xs="12">
              <ValidationProvider
                v-slot="{ errors }"
                name="web"
                rules=""
              >
                <!-- <label for="web">Pgina web</label> -->
                <v-text-field
                  ref="web"
                  id="web"
                  v-model="editedItem.web_site"
                  label="Pgina web"
                  placeholder="Pgina web de la venta"
                  outlined
                  dense
                  class="mx-auto rounded-lg"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                >
                </v-text-field>
              </ValidationProvider>
            </v-col>

            <v-col  cols="12" md="6" sm="6" xs="12">
              <ValidationProvider
                v-slot="{ errors }"
                name="address"
                rules="required"
              >
                <v-textarea
                  ref="address"
                  id="address"
                  name="address"
                  v-model="editedItem.address"
                  label="Direccin *"
                  outlined
                  auto-grow
                  rows="1"
                  class="rounded-lg"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                ></v-textarea>
              </ValidationProvider>
            </v-col>

            <v-col cols="12" md="6" sm="6" xs="12">
              <v-btn block :color="invalidCoordenates && !editedItem.coordenates ? 'error' : 'primary'" outlined style="text-transform: none; letter-spacing: initial;" @click="markPosition = true">
                <span class="mr-2">Ubicar en el mapa</span>
                <v-icon size="20">mdi-map-marker</v-icon>
              </v-btn>
            </v-col>

          <!-- Provincia y Municipio -->
            <v-col  cols="12" md="6" sm="6" xs="12">
              <ValidationProvider
                v-slot="{ errors }"
                name="province"
                rules="required"
              >
                <v-select
                  ref="province"
                  id="province"
                  v-model="province"
                  :items="provinces"
                  item-text="name"
                  item-value="name"
                  label="Provincia *"
                  placeholder="Selecionar provincia"
                  outlined
                  dense
                  class="mx-auto rounded-lg"
                  no-data-text="No hay datos"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                ></v-select>
              </ValidationProvider>
            </v-col>

            <v-col  cols="12" md="6" sm="6" xs="12">
              <ValidationProvider
                v-slot="{ errors }"
                name="municipality"
                rules="required"
              >
                <v-select
                  ref="municipality"
                  id="municipality"
                  v-model="editedItem.municipalityId"
                  :items="municipalities"
                  label="Municipio *"
                  placeholder="Seleccionar municipio"
                  outlined
                  dense
                  class="mx-auto rounded-lg"
                  no-data-text="No hay datos"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                ></v-select>
              </ValidationProvider>
            </v-col>

          <!-- Entrega a domicilio -->
            <v-col cols="12" md="6" sm="6" xs="6">
              <v-checkbox
                id="homeDelivery"
                v-model="editedItem.delivery"
                label="Realizan entregas a domicilio"
              ></v-checkbox>
            </v-col>

          <!-- Hora de apertura y cierre -->
            <v-col>
              <v-row class="pa-3" justify="space-between">
                <label>Dias de trabajo</label>
                <v-btn icon @click="addWorkingDay">
                  <v-icon>mdi-plus</v-icon>
                </v-btn>
              </v-row>
              <v-row v-for="(hour, index) in editedItem.opening_hours" :key="index">
                <!-- <v-col class="pa-0 text-end" cols="12" justify="end">
                  <v-btn color="red" icon @click="removeWorkingDay(index)">
                    <v-icon>mdi-minus</v-icon>
                  </v-btn>
                </v-col> -->
                <v-col cols="4" md="6" sm="6" xs="6" class="px-0 pt-0">
                  <ValidationProvider
                    v-slot="{ errors }"
                    :name="'workingDay_day_'+index"
                  >
                    <v-select
                      :ref="'workingDay_day_'+index"
                      :id="'workingDay_day_'+index"
                      v-model="hour.day"
                      :items="days"
                      label="Da"
                      outlined
                      dense
                      class="mx-auto rounded-lg"
                      no-data-text="No hay datos"
                      :error-messages="errors"
                      :hide-details="errors.length > 0 ? false : true"
                    ></v-select>
                  </ValidationProvider>
                </v-col>
                <v-col cols="4" md="6" sm="6" xs="6" class="pr-0 pt-0">
                  <ValidationProvider
                    v-slot="{ errors }"
                    :name="'workingDay_opening_'+index"
                  >
                    <v-text-field
                      :ref="'workingDay_opening_'+index"
                      :id="'workingDay_opening_'+index"
                      v-model="hour.opening"
                      type="time"
                      label="Apertura"
                      color="primary"
                      persistent-hint
                      class="mx-auto rounded-lg"
                      :error-messages="errors"
                      :hide-details="errors.length > 0 ? false : true"
                      dense
                      outlined
                    ></v-text-field>
                  </ValidationProvider>
                </v-col>
                <v-col cols="4" md="6" sm="6" xs="6" class="pr-0 pt-0">
                  <ValidationProvider
                    v-slot="{ errors }"
                    :name="'workingDay_closing_'+index"
                  >
                    <v-text-field
                      :ref="'workingDay_closing_'+index"
                      :id="'workingDay_closing_'+index"
                      v-model="hour.closing"
                      type="time"
                      label="Cierre"
                      color="primary"
                      persistent-hint
                      class="mx-auto rounded-lg"
                      :error-messages="errors"
                      :hide-details="errors.length > 0 ? false : true"
                      dense
                      outlined
                    ></v-text-field>
                  </ValidationProvider>
                </v-col>
              </v-row>
            </v-col>

            <v-col cols="12">
              <label for="description">Descripcin</label>
              <v-textarea
                ref="description"
                id="description"
                name="description"
                v-model="editedItem.description"
                placeholder="Breve descripcion de la venta"
                outlined
                :readonly="isAdmin"
                hide-details
                class="rounded-lg"
              ></v-textarea>
            </v-col>

          <!-- <div class="my-5" v-if="editedItem.homeDelivery">
            <v-row class="pa-3" justify="space-between">
              <label>Destinos de entrega</label>
              <v-btn icon @click="addDeliveryProvince">
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </v-row>
            <ProvinceMunicipality v-for="(province, i) of deliveryProvinces" :key="i"
                :required="editedItem.homeDelivery"
                :provinceProp="province"
                :municipalityProp="editedItem.deliveryMunicipalities"
                :index="i"
                @updateSelection="updateSelection($event)"
                @remove="removeDeliveryProvince(index, province)" />
          </div> -->

          </v-row>

        </form>
      </ValidationObserver>

      <GoogleMapsMarker
        :showMap="markPosition"
        :currentPosition="coordenates"
        :readonly="isAdmin"
        @closeMap="markPosition = false"
        @savePosition="editedItem.coordenates = $event" />
    </v-container>
  </v-main>
</template>

<script>
import { dateRules } from '@/mixins/date_rules'
import { numberRules } from '@/mixins/number_rules'
import { generalRules } from '@/mixins/general_rules'
import { apiServices } from '@/mixins/api.services'
import FileUpload from '@/components/image-upload/FileUpload'
import provinces from '@/nomencladores/provinces.json'
import storeService from '@/axios/store.services.js'
// import ProvinceMunicipality from '@/components/ProvinceMunicipality.vue'
import GoogleMapsMarker from '@/components/GoogleMapsMarker/GoogleMapsMarker.vue'
import BackButton from '../../../components/BackButton.vue'

export default {
  name: 'CreateStore',
  components: {
    FileUpload,
    GoogleMapsMarker,
    // ProvinceMunicipality,
    BackButton
  },
  props: {

  },
  data () {
    return {
      editedItem: this.$route.params.store,
      invalidCoordenates: false,
      emptyImageError: false,
      opening: null,
      closing: null,
      open_date_menu: false,
      closed_date_menu: false,
      markPosition: false,
      provinces: provinces,
      province: null,
      storeName: null,
      deliveryProvinces: [null],
      deliveryMunicipalities: [],
      days: ['Todos los dias', 'Lunes', 'Martes', 'Mircoles', 'Jueves', 'Viernes', 'Sbado', 'Domingo'],
      newStore: {
        id: null,
        name: null,
        description: null,
        slug: null,
        phone: null,
        email: null,
        web_site: null,
        delivery: false,
        photo: null,
        address: null,
        municipalityId: null,
        coordenates: null,
        opening_hours: [
          { day: null, opening: null, closing: null }
        ]
      }
    }
  },
  mixins: [numberRules, dateRules, generalRules, apiServices],
  created () {
    // this.loadFoodTypes()
    // this.loadPrices()
    // this.buildDeliveryMunicipalities()
    this.editedItem = this.editedItem == null ? this.newStore : this.editedItem
    this.storeName = this.editedItem.name || ''

    // if (!this.editedItem.deliveryTime) {
    //   this.editedItem.deliveryTime = {
    //     value: null,
    //     unit: null
    //   }
    // }
    // if (!this.editedItem.deliveryMaxTime) {
    //   this.editedItem.deliveryMaxTime = {
    //     value: null,
    //     unit: null
    //   }
    // }
    // if (!this.editedItem.maxDistance) {
    //   this.editedItem.maxDistance = {
    //     value: null,
    //     unit: 'Km'
    //   }
    // }
  },
  watch: {
    // opening (val) {
    //   this.editedItem.opening = this.formatDate(this.opening)
    // },
    // closing (val) {
    //   this.editedItem.closing = this.formatDate(this.closing)
    // }
  },
  computed: {
    municipalities: function () {
      if (this.province) {
        return this.provinces.find(p => p.name === this.province).municipalities
      }
      return []
    },

    formTitle () {
      return this.editedItem.id ? 'Editar tienda' : 'Nueva tienda'
    },

    isAdmin () {
      return this.$store.getters.isAdmin()
    },

    coordenates () {
      return this.editedItem.address !== null ? this.editedItem.address.coordenates : ''
    }
  },
  methods: {
    close () {
      for (const key in this.$refs) {
        if (key !== 'observer' && key !== 'open_date_menu' && key !== 'closed_date_menu') {
          this.$refs[key].reset()
        }
      }
      this.$emit('close')
    },

    save () {
      // this.invalidCoordenates = this.editedItem.address.coordenates === null
      this.invalidCoordenates = false
      // this.emptyImageError = this.editedItem.image === null
      this.emptyImageError = false
      this.$refs.observer.validate().then((result) => {
        if (result && !this.invalidCoordenates && !this.emptyImageError) {
          this.editedItem.slug = this.slugify(this.editedItem.name)
          if (!this.editedItem.image) {
            delete this.editedItem.image
          }
          // this.cleanData()

          if (!this.editedItem.id) {
            // storeService.addStore(this.editedItem).then(response => {
            //   if (response) {
            //     this.editedItem = response.data
            //     this.$toast.success('Guardado', {
            //       timeout: 2000
            //     })
            //     this.$toast.success('Se ha enviado un correo de activacin a su cuenta de correo', {
            //       timeout: 3500
            //     })
            //     this.$emit('save')
            //     this.$router.back()
            //   }
            // })
            //   .catch(() => {
            //     this.$toast.error('Error al guardar')
            //   })
            this.$store.dispatch('createStore', this.editedItem)
            this.$router.back()
          } else {
            storeService.updateRestaurant(this.editedItem).then(response => {
              if (response) {
                this.editedItem = response.data
                this.$toast.success('Guardado', {
                  timeout: 2000
                })
                this.$emit('save')
              }
            })
              .catch(() => {
                this.$toast.error('Error al guardar')
              })
          }
        } else {
          this.$toast.warning('Rellene correctamente los campos')
        }
      })
    },

    imageAdded (event) {
      this.editedItem.oldImage = this.editedItem.image
      this.editedItem.image = event
      this.emptyImageError = this.editedItem.image === null
    },

    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },

    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },

    slugify (content) {
      return content.toLowerCase().replaceAll(/ /g, '-').replaceAll(/[^\w-]+/g, '')
    },

    cleanData () {
      for (const key in this.editedItem) {
        if (key !== 'foodType' && key !== 'address' && key !== 'telegram') {
          if (this.editedItem[key] === null) {
            delete this.editedItem[key]
          }
        }
        if (key === 'homeDelivery') {
          if (!this.editedItem[key]) {
            // delete this.editedItem.deliveryTime
            // delete this.editedItem.deliveryMaxTime
            // delete this.editedItem.maxDistance
            delete this.editedItem.deliveryMunicipalities
          }
        }
      }
    },

    buildDeliveryMunicipalities () {
      this.editedItem.deliveryMunicipalities.forEach(munic => {
        if (munic !== 'Todos los municipios') {
          var province = this.provinces.find(p => {
            if (p.municipalities.includes(munic)) {
              return p
            }
          }).name
          this.deliveryProvinces = []
          if (!this.deliveryProvinces.includes(province)) {
            this.deliveryProvinces.push(province)
          }
        }
      })
    },

    addDeliveryProvince () {
      this.deliveryProvinces.push(null)
    },

    addWorkingDay () {
      this.newStore.opening_hours.push({ day: null, opening: null, closing: null })
    },

    removeWorkingDay (index) {
      this.opening_hours.splice(index, 1)
    },

    removeDeliveryProvince (index, province) {
      if (province !== null) {
        var municipalities = this.provinces.find(p => p.name === province).municipalities
        for (let i = 0; i < this.editedItem.deliveryMunicipalities.length; i++) {
          if (municipalities.includes(this.editedItem.deliveryMunicipalities[i])) {
            this.editedItem.deliveryMunicipalities = this.editedItem.deliveryMunicipalities.splice(i, 1)
          }
        }
      }
      this.deliveryProvinces.splice(index, 1)
    },

    updateSelection (event) {
      this.editedItem.deliveryMunicipalities = event
      this.buildDeliveryMunicipalities()
    },

    updateCoordenates (event) {
      this.editedItem.address.coordenates = event
      this.invalidCoordenates = this.editedItem.address.coordenates === null
    }

  }
}
</script>

<style>

</style>
