<template>
  <v-hover v-slot="{ hover }">
    <v-card max-width="300" height="300" max-height="300" class="ma-2 rounded-lg" :class="{ 'on-hover': hover }">
      <v-img max-width="300" height="300" max-height="300" :src="url" :lazy-src="url" contain aspect-ratio="1.4">
        <v-row v-if="removable" :class="hover ? 'fill-height flex-column dropback ma-0' : 'fill-height flex-column ma-0'" justify="center"></v-row>
        <div v-if="removable" class="align-self-center center">
          <v-btn
            :class="{ 'show-btns': hover }"
            color="transparent"
            icon
            @click="remove"
          >
            <v-icon
              :class="{ 'show-btns': hover }"
              color="transparent"
              size="48"
              v-text="'mdi-close-circle-outline'"
            ></v-icon>
          </v-btn>
        </div>
      </v-img>
    </v-card>
  </v-hover>
</template>

<script>
export default {
  name: 'ImageCard',
  props: {
    url: {
      type: String,
      require: true
    },
    removable: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    remove () {
      this.$emit('remove')
    }
  }
}
</script>

<style lang="scss" scoped>
.v-card {
  transition: opacity .4s ease-in-out;
}

// .v-card:hover {
//   opacity: 0.4;
//  }

.dropback:hover{
  background-color: #b71c1c;
  opacity: 0.1;
  color: white !important;
}

.show-btns {
  color: white !important;
}

.center {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
}
</style>
