import axiosCustom from '../axios'

export default class OfferServices {
  static getAllOffers (storeId) {
    return axiosCustom.get(`/service/store/${storeId}/offer`, { params: {} })
  }

  static getOffersByCategory (data) {
    return axiosCustom.get(`/service/store/${data.store}/offer/${data._id}`)
  }

  static getOfferById (data) {
    return axiosCustom.get(`/service/store/${data.store}/offer/${data._id}`)
  }

  static addOffer (data) {
    return axiosCustom.post(`/service/store/${data.store}/offer`, data)
  }

  static updateOffer (data) {
    return axiosCustom.patch(`/service/store/${data.store._id}/offer/${data._id}`, data)
  }

  static updateOfferStatus (data) {
    return axiosCustom.patch(`/service/store/${data.store._id}/offer/${data._id}/change-status`)
  }

  static deleteOffer (data) {
    return axiosCustom.delete(`/service/store/${data.store._id}/offer/${data._id}`)
  }
}
