<template>
  <v-main>
      <v-container id="confirmation-container">
        <v-col id="login-form" style="width: 369px; height: auto">
          <h1 class="text-bolder text-center mb-8">Activar Usuario</h1>
          <v-card class="rounded-xl">
            <v-col align-self="center">
              <ValidationObserver ref="observer">
                <v-row align="center" class="mt-5">
                  <v-col>
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="code"
                      rules="required"
                    >
                      <v-text-field
                        ref="code"
                        id="code"
                        v-model="code"
                        placeholder="Cdigo de activacin"
                        outlined
                        full-width
                        @input="checkCode"
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      >
                      </v-text-field>
                    </ValidationProvider>
                  </v-col>
                </v-row>
                <v-row align="center" justify="space-around">
                  <v-col align="center">
                    <p class="text-info"><span class="link" @click="reSendEmail">Volver a enviar el cdigo</span></p>
                  </v-col>
                </v-row>
                <v-row align="center" justify="space-around">
                  <v-col align="center">
                    <v-btn class="rounded-lg text-bold shadow" color="secondary" @click="verifyEmail">Verificar</v-btn>
                  </v-col>
                </v-row>
                <v-row align="center" justify="space-around">
                  <v-col align="center">
                    <p>Si ya tienes una cuenta en CubaMax, <router-link class="text-info" to="/iniciar">Inicia sesin</router-link></p>
                    <!-- <p>Si eres nuevo en CubaMax, <router-link class="text-info" to="/registrarse">Regstrate</router-link></p> -->
                  </v-col>
                </v-row>
              </ValidationObserver>
            </v-col>
          </v-card>
        </v-col>
      </v-container>
  </v-main>
</template>

<script>
import authenticationServices from '@/axios/authentication.services'
import router from '../../router'

export default {
  name: 'Confirmation',
  data () {
    return {
      code: null,
      sendEmail: this.$route.params.sendEmail || false,
      user: this.$route.params.user
    }
  },
  created () {
    if (!this.$route.params.user) {
      router.replace('/iniciar')
    }
    if (this.sendEmail) {
      this.reSendEmail()
    }
  },
  computed: {
    checkCode () {
      if (this.code && this.code.length === 6) {
        this.verifyEmail()
      }
    }
  },
  methods: {
    verifyEmail () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          authenticationServices.verifyEmail({ code: this.code }).then((result) => {
            if (result) {
              authenticationServices.login(this.user).then((result) => {
                if (result) {
                  localStorage.setItem('store_user', JSON.stringify(result.data))
                  this.$toast.success('Bienvenido')
                  router.push({ path: '/' })
                }
              }).catch(() => {
                this.$toast.success('Usuario activado!')
                this.$toast.error('Error de iniciar')
                router.push({ path: '/iniciar' })
              })
            }
          }).catch(() => {
            this.$toast.error('Error al verificar su cdigo')
          })
        }
      })
    },
    reSendEmail () {
      authenticationServices.reSendEmail({ type: 1, email: this.user.email }).then((result) => {
        if (result) {
          this.$toast.success('Se ha enviado un cdigo a su correo')
        }
      }).catch(() => {
        this.$toast.error('No se pudo enviar el cdigo de verificacin')
      })
    }
  }
}
</script>

<style lang="scss" scoped>
#login-form{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}

.row{
  margin: 0
}

.link{
  cursor: pointer;
}

input{
  text-align: center !important;
}
</style>
