<template>

    <div :class="['product-card', {'single-view': activateSingleMode}]">

        <v-card
          class="ma-2 rounded-lg no-elevation"
        >
          <!-- <v-card-title class="d-flex flex-row-reverse">
          </v-card-title> -->
          <v-img contain
            :src="getImgUrl(random + '.jpg')"
            :lazy-src="getImgUrl(random + '.jpg')"
            class="white--text rounded-lg"
            height="335px"
            style="border-top-left-radius: inherit; border-top-right-radius: inherit;"
            @click="goToDetails(product)"
          >
              <v-row class="mt-0 text-end">
                <v-col>
                  <ProductOptionDialog />
                </v-col>
              </v-row>
              <v-row style="position: absolute; bottom: 13px;">
                <v-col>
                  <!-- <v-btn
                    icon
                    dark
                    :color="like ? 'red' : 'white'"
                    @click="onLike"
                  >
                    <v-icon>
                      {{ like ? 'mdi-heart' : 'mdi-heart-outline' }}
                    </v-icon>
                  </v-btn> -->
                </v-col>
              </v-row>
          </v-img>

          <v-card-text class="text--primary pt-2 px-1 pb-0">
            <div class="mb-0 card-product-name text-2-line-truncate">{{ product.name }}</div>
          </v-card-text>

          <v-card-actions class="px-1 py-0">
            <v-col class="px-0">
              <div id="price">{{ product.price }} <span class="currency-label"> CUP</span></div>
            </v-col>
            <v-col class="px-0" align="end" style="font-size: 14px">
              <!-- <v-btn  class="mx-0 px-0"text @click="goToDetails(product)"> -->
              <!-- <span @click="goToDetails(product)">Ver</span> -->
              <v-btn :class="like ? '' : 'teal lighten-5'"
                icon
                dark
                :color="like ? 'red' : 'grey lighten-2'"
                @click="onLike"
              >
                <v-icon>
                  {{ like ? 'mdi-heart' : 'mdi-heart-outline' }}
                </v-icon>
              </v-btn>
              <!-- </v-btn> -->
            </v-col>
          </v-card-actions>
        </v-card>

      <v-dialog v-model="showImageDialog" style="box-shadow: none !important;" max-width="650px" color="transparent" elevation="0">
        <v-card color="transparent" elevation="0" max-height="650" max-width="650">
          <v-row class="fill-height" justify="center">
            <v-img contain
              max-height="575px"
              :src="getImgUrl(random + '.jpg')"
              :lazy-src="getImgUrl(random + '.jpg')"
            ></v-img>
          </v-row>
        </v-card>
      </v-dialog>

    </div>

</template>

<script>
import router from '../../router'
import store from '../../store'
import ProductOptionDialog from './ProductOptionDialog.vue'

export default {
  name: 'Product',
  components: {
    ProductOptionDialog
  },
  props: {
    product: {
      type: Object,
      required: true
    },
    singleMode: {
      type: Boolean,
      default: false,
      required: false
    }
  },
  data () {
    return {
      showImageDialog: false,
      activateSingleMode: this.singleMode,
      like: false
    }
  },
  watch: {
    singleMode (newValue, oldValue) {
      this.activateSingleMode = newValue
    }
  },
  computed: {
    amoutn: {
      get () {
        return store.getters.checkProductOnCart(this.product.id)
      }
    },
    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': {
          if (this.singleMode) {
            this.activateSingleMode = true
            return this.activateSingleMode
          }
          return false
        }
        case 'sm': {
          this.activateSingleMode = false
          return (this.$vuetify.breakpoint.mobile || window.innerWidth < 865) || false
        }
        case 'md': {
          this.activateSingleMode = false
          return true
        }
        case 'lg': {
          this.activateSingleMode = false
          return true
        }
        case 'xl': {
          this.activateSingleMode = false
          return true
        }
      }
    },
    random () {
      var x = parseInt(Math.random() * 10)
      return x > 5 ? x - 5 : x
    }
  },
  methods: {
    onLike () {
      this.like = !this.like
    },

    addToCart (product) {
      this.$store.dispatch('addToCart', product)
    },
    checkOnFavorites (product) {
      if (product.isFavorite) {
        this.$store.dispatch('addToFavorites', product)
      } else {
        this.$store.dispatch('removeFromFavorites', product)
      }
      product.isFavorite = !product.isFavorite
    },
    getImgUrl (image) {
      var images = require.context('../../assets/', false)
      return images('./' + image)
    },
    goToDetails (product) {
      // router.push({ path: '/store/' + this.product.section.toLowerCase() + '/' + this.product.id + '/details' })
      router.push({ path: `/venta/${product.sale.slug}/${product.category.slug}/${product.slug}` })
    }
  }
}
</script>

