export const numberRules = {
  data () {
    return {
      isNumber: v => {
        if (!v.trim()) {
          return true
        }
        if (!isNaN(parseInt(v)) && v > 0) {
          return true
        }
        return 'Introduzca nmero vlidos'
      },
      id_number: v => {
        if (!isNaN(parseInt(v)) && v > 0 && v.length === 12) {
          return true
        }
        return 'Su nmero debe contener 12 dgitos'
      },
      more_than_zero: v => {
        return v >= 1
      },
      positive_number: v => {
        return v >= 0
      }
      // min: v => v >= this.form.min || `The Min is ${this.form.min}`,
      // max: v => v <= this.form.max || `The Max is ${this.form.max}`
    }
  }
}
