<template>
  <v-main>
    <v-app-bar
      absolute
      color="white"
      elevate-on-scroll
      class="header-toolbar"
      style="position: fixed; z-index: 8; width: 100%;"
    >
      <back-button />

      <v-toolbar-title>Mi Venta</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon class="mr-2" @click="editStore" v-if="store">
        <v-icon>mdi-pencil</v-icon>
      </v-btn>
    </v-app-bar>
    <v-container id="store-tabs-container"  :class="{'fill-height': !store}" class="mt-11 px-0">

      <v-row class="fill-height align-center" v-if="!store">
        <v-col class="text-center">
          <v-btn
            elevation="0"
            width="120"
            height="120"
            class="create-button rounded-lg"
            @click="createStore"
          >
            <template v-slot:default>
              <v-col>
                <v-icon size="75">mdi-store-plus</v-icon>
                <p>Crear</p>
              </v-col>
            </template>
          </v-btn>
        </v-col>
      </v-row>

      <v-row class="pt-md-6 pt-sm-6 px-md-6 px-sm-6 pa-0 ma-0" justify="space-around" v-if="store">
        <v-col class="pa-0" cols="12" md="6">
          <v-row class="ma-0">
            <v-img contain max-height="398" height="min-content" :width="properties.mainImageWidth" class="blog-box-shadow"
              :src="getImgUrl(store.photo)" :lazy-src="getImgUrl(store.photo)">
            </v-img>
          </v-row>
        </v-col>

        <div class="px-3">
          <v-col cols="12">
            <p id="title" class="mb-2 px-md-6 px-sm-6">{{ store.name }}</p>
            <v-row>
              <rating rated readonly @ratingDone="addNewRate($event)"></rating>
            </v-row>
          </v-col>

          <v-row>
            <v-col cols="12">
              <v-btn block outlined @click="showProductList">
                Productos de venta
              </v-btn>
            </v-col>

            <!-- <v-col cols="12">
              <v-btn block outlined>
                Mostrar informacin
              </v-btn>
            </v-col> -->
          </v-row>

          <v-divider class="mt-4"></v-divider>

          <v-col cols="12" class="mt-3">

            <p class="text-bold mb-1">Horario:</p>
            <div class="mb-4">
              <p class="mb-1" v-for="(hour, i) in store.opening_hours" :key="i">{{ `${hour.day}: ${hour.opening} - ${hour.closing}` }}</p>
            </div>

            <p class="text-bold mb-1">Direccion:</p>
            <p>{{ store.address }}</p>

            <v-row class="ma-0 pb-3" justify="end" v-if="store.coordenates">
              <v-btn block color="primary" outlined style="text-transform: none; letter-spacing: initial;">
                <span class="mr-2">Ver en el mapa</span>
                <v-icon size="20">mdi-map-marker</v-icon>
              </v-btn>
            </v-row>
            <p class="text-bold mt-3" v-if="store.delivery">
              <v-icon class="mr-2" size="28">mdi-moped</v-icon>
              Brindamo servicio a domicilio
            </p>

            <p class="text-bold mb-1">Pagina web:</p>
            <p>{{ store.web_site }}</p>

            <v-row justify="space-around">
              <v-col><v-icon class="mr-2">mdi-phone</v-icon>{{ store.phone }}</v-col>
              <v-col v-if="store.whatsapp"><v-icon class="mr-2">mdi-whatsapp </v-icon>{{ store.whatsapp }}</v-col>
              <!-- <v-col><v-icon>mdi-telegram </v-icon>52662623</v-col> -->
            </v-row>
            <p class="mt-4 text-justify">{{ store.description }}</p>
          </v-col>
        </div>
      </v-row>

    </v-container>
  </v-main>
</template>

<script>
import BackButton from '../../../components/BackButton.vue'
import Rating from '../../../components/Rating/Rating.vue'

export default {
  name: 'MyStore',
  components: {
    BackButton,
    Rating
  },
  data () {
    return {
      loading: false,
      address: '',
      createNewGarageSale: false,
      dialog: false,
      dialogDelete: false,
      editedIndex: -1,
      options: {
        duration: 300,
        offset: 150,
        easing: 'easeInOutCubic'
      },
      properties: {
        mainImageWidth: 450,
        mainImageHeight: 398,
        bottomBarHeight: '245px'
      },
      store: null,
      days: ['Todos los dias', 'Lunes', 'Martes', 'Mircoles', 'Jueves', 'Viernes', 'Sbado', 'Domingo']
    }
  },
  created () {
    this.store = this.$store.getters.userStore() || null
    if (this.store) {
      this.sortStoreOpeningHours()
    }
  },
  methods: {
    sortStoreOpeningHours () {
      const sortedDays = []
      for (let i = 0; i < this.days.length; i++) {
        const x = this.store.opening_hours.find(x => x.day === this.days[i])
        if (x) {
          sortedDays.push(x)
        }
      }
      this.store.opening_hours = sortedDays
    },
    createStore () {
      this.$router.push({ name: 'create-store' })
    },

    showProductList () {
      this.$router.push({ name: 'store-products', params: { store: this.defaultItem.products } })
    },

    getImgUrl (image) {
      if (!image) {
        image = 'casa-en-venta.png'
      }
      var images = require.context('../../../assets', false)
      return images('./' + image)
    },

    editStore (item) {
      this.$router.push({ name: 'create-store', params: { store: this.store } })
    },

    deleteItem (item) {
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    }
  }
}
</script>

<style lang="scss" scoped>
  .v-item-group.v-slide-group.v-tabs-bar {
    background: transparent !important;
  }
  .create-button {
    height: 120px;
    width: 120px;
    background-color: white !important;
    color: darkgrey !important;
    border: double;
    border-color: lightgrey !important;
    margin-top: -90px;
  }
  #title{
    text-align: left;
    font-family: Raleway-bold;
    font-style: normal;
    font-weight: bold;
    font-size: 25px;
    color: rgba(0,100,100,1);
    text-transform: none;
  }
</style>
