const state = {
  province: null,
  municipality: null,
  department: null,
  userIsLogued: false,
  userIsAdmin: false,
  userInfo: null,
  drawer: false,
  search: null,
  searchLocationDialog: false
}

const mutations = {

  setSearch (state, search) {
    state.province = search.province
    state.municipality = search.municipality
    state.department = search.department
    state.search = search
    localStorage.setItem('store_search', JSON.stringify(search))
  },

  logOut (state) {
    state.userIsLogued = false
    state.userIsAdmin = false
    state.userInfo = null
    localStorage.clear()
    localStorage.setItem('store_search', JSON.stringify(state.search))
  },

  changeUserAdminStatus (state, status) {
    state.userIsAdmin = status
  },

  logIn (state, info) {
    state.userInfo = info
    state.userIsLogued = true
    state.userIsAdmin = info.user.role.name === 'Admin'
    localStorage.setItem('store_user', JSON.stringify(info))
  },

  updateUserInformation (state, info) {
    state.userInfo = info
    state.userIsAdmin = info.user.role.name === 'Admin'
    localStorage.setItem('store_user', JSON.stringify(info))
  },

  loadLocalUserInformation () {
    if (localStorage.getItem('store_user')) {
      if (JSON.parse(localStorage.getItem('store_user')).user.role && JSON.parse(localStorage.getItem('store_user')).user.role.name === 'Admin') {
        state.userIsAdmin = true
      }
      state.userIsLogued = true
    }
    if (localStorage.getItem('store_search')) {
      state.search = JSON.parse(localStorage.getItem('store_search'))
    }
  },

  showSearchLocationDialog (state) {
    state.searchLocationDialog = !state.searchLocationDialog
  },

  showDrawer (state) {
    state.drawer = !state.drawer
  }
}

const actions = {

  setSearch ({ commit }, search) {
    commit('setSearch', search)
  },

  logOut ({ commit }, status) {
    commit('logOut', status)
  },

  changeUserAdminStatus ({ commit }, status) {
    commit('changeUserAdminStatus', status)
  },

  logIn ({ commit }, info) {
    commit('logIn', info)
  },

  updateUserInformation ({ commit }, info) {
    commit('updateUserInformation', info)
  },

  loadLocalUserInformation ({ commit }) {
    commit('loadLocalUserInformation')
  },

  showSearchLocationDialog ({ commit }) {
    commit('showSearchLocationDialog')
  },

  showDrawer ({ commit }) {
    commit('showDrawer')
  }
}

const getters = {
  userIsLogued: (state) => () => {
    return state.userIsLogued
  },
  isAdmin: (state) => () => {
    return state.userIsAdmin
  },
  userInfo: (state) => () => {
    return state.userInfo
  },
  showDrawer: (state) => () => {
    return state.drawer
  },

  searchLocationDialogStatus: (state) => () => {
    return state.searchLocationDialog
  },
  getLocalStoreSearchData: (state) => () => {
    return state.search
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
