import axiosCustom from '.'

export default class StoreServices {
  static getAllStores () {
    return axiosCustom.get('/store')
  }

  static getStores (data) {
    return axiosCustom.get('/store', { params: data.params })
  }

  static getStoreNames () {
    return axiosCustom.get('/store/names')
  }

  static getStoreById (data) {
    return axiosCustom.get(`/store/${data._id}`)
  }

  static getStoreByUserId (data) {
    return axiosCustom.get(`/store/${data._id}`)
  }

  static getStoreOffers (data) {
    return axiosCustom.get(`/store/${data._id}/offer`)
  }

  static getStoreCategories (_id) {
    return axiosCustom.get(`/store/${_id}/categories`)
  }

  static getStoreOffersByCategory (data) {
    return axiosCustom.get(`/store/${data._id}/offer`, { params: data.params })
  }

  static getStoreRequests (data) {
    if (JSON.parse(localStorage.getItem('store_user')).user.role.name === 'Admin') {
      return axiosCustom.get('/admin/request', { params: data.params })
    } else {
      return axiosCustom.get('/store/requests', { params: data.params })
    }
  }

  static getStoreFoodTypes () {
    return axiosCustom.get('/store/food-type')
  }

  static addStore (data) {
    return axiosCustom.post('/store', data)
  }

  static updateStore (data) {
    return axiosCustom.patch(`/store/${data._id}`, data)
  }

  static deleteStore (data) {
    if (JSON.parse(localStorage.getItem('store_user')).user.role.name === 'Admin') {
      return axiosCustom.delete(`/admin/store/${data._id}`)
    } else {
      return axiosCustom.delete(`/store/${data._id}`)
    }
  }

  static deleteStoreOffer (data) {
    if (JSON.parse(localStorage.getItem('store_user')).user.role.name === 'Admin') {
      return axiosCustom.delete(`/admin/store/${data.storeId}/offer/${data.offerId}`)
    } else {
      return axiosCustom.delete(`/store/${data.storeId}/offer/${data.offerId}`)
    }
  }

  static calculateDeliveryDistace (data) {
    return axiosCustom.get(`/store/${data.storeId}/calculate-distance?destinations=${data.destinations}`)
  }
}
