<template>
  <div>
    <v-row>
      <v-rating
        background-color="grey"
        color="warning"
        dense
        length="5"
        size="22"
        :value="4.5"
        class="mb-7 px-md-6 px-sm-6"
        :readonly="readonly"
      ></v-rating>
      <v-chip v-if="!rated" class="ma-2 rate-button" x-small @click.stop="rateDialog = true">Valorar</v-chip>
    </v-row>
    <v-dialog
      v-model="rateDialog"
      max-width="290"
    >
      <v-card>
        <v-card-text class="pb-0">
          <v-row>
            <v-col class="my-3 text-center">
              <v-rating
                v-model="rating"
                color="yellow darken-3"
                background-color="grey darken-1"
                hover
                large
                size="27"
              ></v-rating>
            </v-col>
          </v-row>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn class="px-1"
            color="green darken-1"
            text
            small
            @click="rateDialog = false"
          >
            Cancelar
          </v-btn>

          <v-btn class="px-0"
            color="green darken-1"
            text
            small
            @click="makeRate"
          >
            Valorar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  props: {
    rated: {
      type: Boolean,
      require: true
    },
    readonly: {
      type: Boolean,
      require: false
    }
  },
  data () {
    return {
      rateDialog: false,
      rating: 0
    }
  },

  methods: {
    openDialog () {
      this.rateDialog = true
    },
    makeRate () {
      this.$emit('ratingDone', this.rating)
      this.rateDialog = false
    }
  }
}
</script>

<style>
.row{
  margin: 0;
}

.v-rating > button.v-icon {
  padding: 0 !important;
}
.rate-button {
  margin: 15px 8px 8px !important;
}
</style>
