<script>
import Product from '@/components/Store/Product'
import FilterDialog from '@/components/FilterDialog'
import saleServices from '@/axios/store.services'

export default {
  name: 'ProductsList',
  props: {
    slideMode: {
      type: Boolean,
      default: false
    },
    listSize: {
      type: Number,
      required: false
    },
    store: {
      type: Object,
      required: false
    }
  },
  components: {
    Product,
    FilterDialog
  },
  data () {
    return {
      pagination: {
        limit: 12,
        offset: 0,
        page: 1,
        filter: null,
        order: '-name',
        total: 0
      },
      filter: {
        _id: null,
        params: {
          category: 'all',
          color: [],
          size: [],
          department: null
        }
      },
      singleMode: true,
      loading2: false,
      filterDialog: false,
      storeSlug: this.$route.params.slug,
      categorySelected: null,
      categorySlugPath: this.$route.params.category_slug || null,
      categories: [
        // { name: 'Todas las categorias', icon: 'mdi-account', _id: 0, slug: 'todas-las-categorias' },
        { name: 'Accesorios', icon: 'mdi-account', _id: 1, slug: 'accesorios' },
        { name: 'Artesania', icon: 'mdi-account', _id: 2, slug: 'artesania' },
        { name: 'Decoracion', icon: 'mdi-flag', _id: 3, slug: 'decoracion' },
        { name: 'Del Hogar', icon: 'mdi-flag', _id: 4, slug: 'del-hogar' },
        { name: 'Ropa', icon: 'mdi-clock', _id: 5, slug: 'ropa' },
        { name: 'Tecnologia', icon: 'mdi-flag', _id: 6, slug: 'tecnologia' }
      ],
      sizes: [
        { name: 'S', value: 'S' },
        { name: 'M', value: 'M' },
        { name: 'L', value: 'L' },
        { name: 'XL', value: 'XL' },
        { name: 'XXL', value: 'XXL' }
      ],
      colors: [
        'red', 'blue', 'green', 'orange', 'black', 'pink', 'grey', 'yellow', 'brown'
      ],
      products: this.$store.getters.products(),
      pageProducts: []
    }
  },
  created () {

  },
  watch: {
    '$route' (to, from) {
      console.log(to)
      if (to.params.product_slug) {
        this.showSaleInformation = false
      } else {
        this.showSaleInformation = true
      }
    },
    store (oldValue, newValue) {
      if (newValue) {
        this.filter.id = this.store._id

        // loading store categories
        saleServices.getStoreCategories(this.store._id).then((result) => {
          this.categories = result.data
          this.categories = this.categorySort(this.categories)

          // getting the category id
          this.categorySlugPath = this.categories.find(cat => cat.slug === this.categorySlugPath)
          this.categorySlugPath = this.categorySlugPath ? this.categorySlugPath._id : null
          this.filter.params.category = this.categorySlugPath || null
          if (this.filter.params.category === null && this.categories.length > 0) {
            this.filter.params.category = this.categories[0]._id
          }

          // starting loading spinner
          this.loading2 = true

          saleServices.getStoreOffersByCategory(this.filter).then((result) => {
            this.menu = result.data.filter(item => { return item.isActive === true })

            this.loading2 = false
            if (this.productSlugPath) {
              this.$nextTick(() => {
                this.$vuetify.goTo('#' + this.productSlugPath, this.options)
              // document.querySelector(this.productSlugPath).click()
              })
            }
          }).catch(() => {
            this.$toast.error('Error al cargar productos')
          })
        }).catch(() => {
        // this.$toast.error('Error al cargar los tipos de comidas')
        })
      }
    },

    categorySelected () {
      this.menu = []
      this.pathCategory = null

      if (this.categories.length > 0) {
        var category = this.categories[this.categorySelected]
        this.filter.params.category = category._id
      }
      if (this.productSlugPath) {
        // router.push({ path: '/stores/' + this.storeSlug + '/' + category.slug })
        this.hash = '#' + category.slug
        this.$nextTick(() => {
          this.$vuetify.goTo(this.hash, { duration: 0, offset: 150 })
        })
      } else {
        this.hash = '#' + category.slug
      }

      this.loading2 = true

      saleServices.getStoreOffersByCategory(this.filter).then((result) => {
        this.menu = result.data.filter(item => { return item.isActive === true })
        this.loading2 = false
        if (this.productSlugPath) {
          this.$nextTick(() => {
            this.$vuetify.goTo('#' + this.productSlugPath, this.options)
            // document.querySelector(this.productSlugPath).click()
          })
        }
      }).catch(() => {
        this.loading2 = false
        this.$toast.error('Error al cargar las ofertas')
      })
    }
  },
  computed: {
    breakpoint () {
      // return window.innerWidth < 865
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': {
          return true
        }
        case 'sm': {
          return (this.$vuetify.breakpoint.mobile || window.innerWidth < 865) || false
        }
        case 'md': {
          return false
        }
        case 'lg': {
          return false
        }
        case 'xl': {
          return false
        }
      }
    },
    isMobile () {
      if (this.$vuetify.breakpoint.mobile || window.innerWidth < 700) {
        return true
      }
      return false
    }
  },
  methods: {
    searchProduct () {
      alert('AAA')
    },
    addToCart (product) {
      this.$store.dispatch('addToCart', product)
    },
    checkOnFavorites (product) {
      if (product.isFavorite) {
        this.$store.dispatch('addToFavorites', product)
      } else {
        this.$store.dispatch('removeFromFavorites', product)
      }
      product.isFavorite = !product.isFavorite
    },
    getImgUrl (image) {
      var images = require.context('../../assets', false, /\.jpeg$/)
      return images('./' + image + '.jpeg')
    },
    seeDetails (product) {
      this.$store.dispatch('updateProductDetails', product)
    },
    paginate () {
      this.pagination.offset = this.pagination.limit * this.pagination.page - this.pagination.limit
      this.pageProducts = this.products.slice(this.pagination.offset, this.pagination.limit * this.pagination.page)
    }
  }
}
</script>

