<template>
  <v-dialog v-model="showMapDialog" max-width="820" class="pa-0"
      :fullscreen="isMobile ? true : false"
      :hide-overlay="isMobile ? true : false"
      :transition="isMobile ? 'dialog-bottom-transition' : ''"
      @click:outside="closeMap">
    <v-card>
      <v-card-text class="px-0 pb-6">
        <GmapAutocomplete v-if="!readonly"
          id="gmaps-autocomplete"
          ref="autocomplete"
          @place_changed="setPlace"
          placeholder="Buscar punto de referencia"
          :options="autocompleteOptions"
          style="width: 100%; height: 40px; padding: 0 15px"
        />
        <GmapMap
          ref="mapRef"
          :center="center"
          :zoom="this.currentPosition && this.currentPosition.split(',').length === 2 ? 15 : 7"
          :options="mapOptions"
          :style="isMobile ? 'height: 80vh;' : 'height: 70vmin'"
          @click="handleMapClick"
        >
          <GmapMarker
            ref="myMarker"
            :position="marker.position"
            :clickable="true"
            :draggable="!readonly"
            @drag="handleMarkerDrag"
            @click="panToMarker"
          />
        </GmapMap>
        <!-- <p>Selected Position: {{ marker.position }}</p> -->
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn class="text-bolder" text @click="closeMap">Cerrar</v-btn>
        <v-btn v-if="!readonly" class="text-bolder" color="secondary" :disabled="marker.position === null" @click="removePosition">Borrar</v-btn>
        <v-btn v-if="!readonly" class="text-bolder" color="primary" :disabled="marker.position === null" @click="savePosition">Guardar</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: 'GoogleMapsMarker',
  props: {
    showMap: {
      type: Boolean,
      require: true
    },
    currentPosition: {
      type: String,
      require: false
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      autocompleteOptions: {
        componentRestrictions: {
          country: [
            'cu'
          ]
        }
      },
      marker: { position: { } },
      center: { lat: 23.109820652557012, lng: -82.36103533478133 },
      currentPlace: null,
      places: [],
      mapOptions: {
        zoomControl: true,
        mapTypeControl: false,
        scaleControl: false,
        streetViewControl: false,
        rotateControl: false,
        fullscreenControl: true,
        disableDefaultUi: true
      }
    }
  },
  created () {

  },
  mounted () {
    // this.geolocate()
    if (this.currentPosition && this.currentPosition.split(',').length === 2) {
      this.marker.position = { lat: parseFloat(this.currentPosition.split(',')[0]), lng: parseFloat(this.currentPosition.split(',')[1]) }
      this.center = this.marker.position
    } else {
      this.marker.position = null
    }
  },
  watch: {
    currentPosition () {
      if (this.currentPosition && this.currentPosition.split(',').length === 2) {
        this.marker.position = { lat: parseFloat(this.currentPosition.split(',')[0]), lng: parseFloat(this.currentPosition.split(',')[1]) }
        this.center = this.marker.position
        if (this.$refs.mapRef) {
          this.$refs.mapRef.$mapPromise.then((map) => {
            map.panTo(this.marker.position)
            map.setZoom(15)
          })
        }
      } else {
        this.marker.position = null
      }
    }
  },
  computed: {
    showMapDialog: {
      get () {
        return this.showMap
      },
      set (event) {
        this.showMap = event
        return this.showMap
      }
    },
    isMobile () {
      return (this.$vuetify.breakpoint.mobile && window.innerWidth < 800) || window.innerWidth < 800
    }
  },
  methods: {
    // detects location from browser
    geolocate () {
      navigator.geolocation.getCurrentPosition((position) => {
        this.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        }

        // this.panToMarker()
      })
    },

    setPlace (place) {
      this.currentPlace = place
      var position = {
        lat: this.currentPlace.geometry.location.lat(),
        lng: this.currentPlace.geometry.location.lng()
      }
      this.marker.position = position
      this.center = position
      this.$refs.mapRef.$mapPromise.then((map) => {
        map.panTo(position)
        map.setZoom(18)
      })
    },

    addMarker () {
      if (this.currentPlace) {
        const marker = {
          lat: this.currentPlace.geometry.location.lat(),
          lng: this.currentPlace.geometry.location.lng()
        }
        this.markers.push({ position: marker })
        this.places.push(this.currentPlace)
        this.center = marker
        this.currentPlace = null
      }
    },

    // sets the position of marker when dragged
    handleMarkerDrag (e) {
      this.marker.position = { lat: e.latLng.lat(), lng: e.latLng.lng() }
    },

    // Moves the map view port to marker
    panToMarker () {
      this.$refs.mapRef.$mapPromise.then((map) => {
        map.panTo(this.marker.position)
        map.setZoom(18)
      })
    },

    // Moves the marker to click position on the map
    handleMapClick (e) {
      if (!this.readonly) {
        this.marker.position = { lat: e.latLng.lat(), lng: e.latLng.lng() }
      }
    },

    closeMap () {
      this.$emit('closeMap')
      document.getElementById('gmaps-autocomplete').value = null
      if (this.currentPosition === null || this.currentPosition === '') {
        this.reset()
      } else {
        this.marker.position = { lat: parseFloat(this.currentPosition.split(',')[0]), lng: parseFloat(this.currentPosition.split(',')[1]) }
      }
    },

    removePosition () {
      this.marker.position = null
      this.$emit('savePosition', null)
    },

    savePosition () {
      this.$emit('savePosition', this.marker.position.lat + ',' + this.marker.position.lng)
      this.$emit('closeMap')
    },

    reset () {
      this.$refs.mapRef.$mapPromise.then((map) => {
        map.setZoom(7)
      })
      this.marker.position = null
      this.center = { lat: 23.109820652557012, lng: -82.36103533478133 }
    }
  }
}

</script>

<style lang="scss" scoped>
.v-btn{
  font-size: 11px;
}
</style>
