<template>
  <v-main>
      <v-app-bar
        absolute
        color="white"
        elevate-on-scroll
        class="header-toolbar"
        style="position: fixed; z-index: 8; width: 100%;"
      >
        <back-button />

        <v-toolbar-title>{{ formTitle }}</v-toolbar-title>

        <v-spacer></v-spacer>

        <v-btn text @click="save">Guardar</v-btn>
      </v-app-bar>
      <v-container id="create-menu-container" class="mt-16">

        <ValidationObserver ref="observer">
          <form autocomplete="off">

            <!-- Nombre -->
            <v-row justify="space-between">

              <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                <FileUpload :image="editedItem.images != 'no_image.jpeg' ? editedItem.images : null" @imageAdded="imageAdded($event)"/>
              </v-col>

              <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                  <ValidationProvider
                    v-slot="{ errors }"
                    name="category"
                    rules="required"
                  >
                  <!-- <label for="state">Estado</label> -->
                  <v-select
                    ref="category"
                    id="category"
                    v-model="editedItem.category"
                    :items="categories"
                    item-text="name"
                    item-value="_id"
                    label="Categoras *"
                    placeholder="Seleccionar categora"
                    outlined
                    dense
                    class="mx-auto rounded-lg"
                    no-data-text="No hay datos"
                    @change="updateProductForm"
                    :error-messages="errors"
                    :hide-details="errors.length > 0 ? false : true"
                  ></v-select>
                </ValidationProvider>
              </v-col>

              <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                <ValidationProvider
                  v-slot="{ errors }"
                  name="name"
                  rules="required"
                >
                  <!-- <label for="name">Nombre*</label> -->
                  <v-text-field
                    ref="name"
                    id="name"
                    v-model="editedItem.name"
                    label="Nombre *"
                    placeholder="Nombre del producto"
                    outlined
                    dense
                    class="mx-auto rounded-lg"
                    :error-messages="errors"
                    :hide-details="errors.length > 0 ? false : true"
                  >
                  </v-text-field>
                </ValidationProvider>
              </v-col>

              <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                <ValidationProvider
                  v-slot="{ errors }"
                  name="size"
                  rules="required"
                >
                  <!-- <label for="name">Nombre*</label> -->
                  <v-text-field
                    ref="size"
                    id="size"
                    v-model="editedItem.size"
                    label="Talla *"
                    placeholder="Talla del producto"
                    outlined
                    dense
                    class="mx-auto rounded-lg"
                    :error-messages="errors"
                    :hide-details="errors.length > 0 ? false : true"
                  >
                  </v-text-field>
                </ValidationProvider>
              </v-col>

              <!-- <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                <ValidationProvider
                  v-slot="{ errors }"
                  name="colors"
                  rules="required"
                >
                  <v-text-field
                    ref="colors"
                    id="colors"
                    v-model="editedItem.colors"
                    label="Talla *"
                    placeholder="Talla del producto"
                    outlined
                    dense
                    class="mx-auto rounded-lg"
                    :error-messages="errors"
                    :hide-details="errors.length > 0 ? false : true"
                  >
                  </v-text-field>
                </ValidationProvider>
              </v-col> -->

              <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                <ValidationProvider
                  v-slot="{ errors }"
                  name="provider"
                  rules="required"
                >
                  <v-text-field
                    ref="provider"
                    id="provider"
                    v-model="editedItem.provider"
                    label="Proveedor"
                    placeholder="Nombre del proveedor"
                    outlined
                    dense
                    class="mx-auto rounded-lg"
                    :error-messages="errors"
                    :hide-details="errors.length > 0 ? false : true"
                  >
                  </v-text-field>
                </ValidationProvider>
              </v-col>

          <!-- price y category -->
              <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                <ValidationProvider
                  v-slot="{ errors }"
                  name="product_price"
                  rules="required|positive"
                >
                  <!-- <label for="price">Precio*</label> -->
                  <v-text-field
                    ref="product_price"
                    id="product_price"
                    v-model="editedItem.product_price"
                    label="Precio del producto *"
                    type="number"
                    outlined
                    min="1"
                    dense
                    class="mx-auto rounded-lg currency-selector"
                    :error-messages="errors"
                    :hide-details="errors.length > 0 ? false : true"
                  >
                  <template v-slot:append>
                    <v-select
                      v-model="editedItem.product_price_currency"
                      :items="currencies"
                      label=""
                      single-line
                      hide-details
                      class="inner-select"
                    ></v-select>
                  </template>
                  </v-text-field>
                </ValidationProvider>
              </v-col>

              <v-col cols="12" md="6" sm="6" xs="12" class="pb-1">
                <ValidationProvider
                  v-slot="{ errors }"
                  name="sale_price"
                  rules="required|positive"
                >
                  <!-- <label for="price">Precio*</label> -->
                  <v-text-field
                    ref="sale_price"
                    id="sale_price"
                    v-model="editedItem.sale_price"
                    label="Precio de venta"
                    type="number"
                    outlined
                    min="1"
                    dense
                    class="mx-auto rounded-lg currency-selector"
                    :error-messages="errors"
                    :hide-details="errors.length > 0 ? false : true"
                  >
                  <template v-slot:append>
                    <v-select
                      v-model="editedItem.sale_price_currency"
                      :items="currencies"
                      label=""
                      single-line
                      hide-details
                      class="inner-select"
                    ></v-select>
                  </template>
                  </v-text-field>
                </ValidationProvider>
              </v-col>

              <v-col cols="12" md="12" sm="12" xs="12" class="pb-1">
                  <!-- <label for="price">Identificadores</label> <span>(Escriba 3 palabras para identificar su producto)</span> -->
                  <v-text-field
                    ref="variants"
                    id="variants"
                    v-model="editedItem.key_words"
                    label="Identificadores"
                    type="text"
                    outlined
                    dense
                    hint="Escriba 3 palabras para identificar su producto"
                    class="mx-auto rounded-lg variants"
                    persistent-hint
                  >
                  </v-text-field>
              </v-col>

            <!-- Descripcion -->
              <v-col cols="12">
                <label for="description">Descripcin</label>
                <v-textarea
                  ref="description"
                  id="description"
                  name="description"
                  v-model="editedItem.description"
                  placeholder="Breve descripcion del producto"
                  outlined
                  hide-details
                  :readonly="isAdmin"
                  class="rounded-lg"
                ></v-textarea>
              </v-col>
            </v-row>
          </form>
        </ValidationObserver>

      </v-container>
  </v-main>
</template>

<script>
import { dateRules } from '@/mixins/date_rules'
import { numberRules } from '@/mixins/number_rules'
import { generalRules } from '@/mixins/general_rules'
import { apiServices } from '@/mixins/api.services'
import FileUpload from '@/components/image-upload/FileUpload'
import offersService from '@/axios/offers.services.js'
import BackButton from '../../../../components/BackButton.vue'

export default {
  name: 'ProductForm',
  components: {
    FileUpload,
    BackButton
  },
  mixins: [numberRules, dateRules, generalRules, apiServices],
  props: {
  },
  data () {
    return {
      productName: null,
      editedItem: this.$route.params.store,
      categories: [],
      newProduct: {
        _id: null,
        isActive: true,
        images: null,
        category: null,
        name: null,
        slug: null,
        size: null,
        colors: null,
        description: null,
        provider: null,
        product_price: null,
        product_price_currency: 'CUP',
        sale_price: null,
        sale_price_currency: 'CUP',
        keys_words: null,
        store: {}
      },
      currencies: ['CUP', 'USD', 'MLC']
    }
  },
  created () {
    this.loadCategories()
    this.editedItem = this.editedItem == null ? this.newProduct : this.editedItem
    this.productName = this.editedItem.name || ''
  },
  computed: {
    formTitle () {
      return this.editedItem.id ? 'Editar producto' : 'Nuevo producto'
    },

    price () {
      return Number.parseFloat(this.editedItem.price).toFixed(2)
    },

    isAdmin () {
      return this.$store.getters.isAdmin()
    }
  },
  methods: {
    loadCategories () {
      this.categories = this.$store.getters.categories()
    },

    close () {
      for (const key in this.$refs) {
        if (key !== 'observer') {
          this.$refs[key].reset()
        }
      }
      this.$emit('close')
    },

    imageAdded (event) {
      this.editedItem.oldImage = this.editedItem.image
      this.editedItem.image = event
      if (this.editedItem.image == null) {
        delete this.editedItem.image
      }
      console.log('CHANGE', this.editedItem)
    },

    save () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          if (this.editedItem.image == null) {
            delete this.editedItem.image
          }
          this.editedItem.slug = this.slugify(this.editedItem.name)
          this.editedItem.price = Number.parseFloat(this.editedItem.price).toFixed(2)
          this.cleanData()
          if (this.editedIndex === -1) {
            offersService.addOffer(this.editedItem).then(response => {
              if (response) {
                this.editedItem = response.data
                this.$toast.success('Guardado', {
                  timeout: 2000
                })
                this.$emit('save')
              }
            })
              .catch(() => {
                this.$toast.error('Error al guardar')
              })
          } else {
            offersService.updateOffer(this.editedItem).then(response => {
              if (response) {
                this.editedItem = response.data
                this.$toast.success('Guardado', {
                  timeout: 2000
                })
                this.$emit('save')
              }
            })
              .catch(() => {
                this.$toast.error('Error al guardar')
              })
          }
        }
      })
    },

    slugify (content) {
      return content.toLowerCase().replaceAll(/ /g, '-').replaceAll(/[^\w-]+/g, '')
    },

    cleanData () {
      for (const key in this.editedItem) {
        if (this.editedItem[key] === null) {
          delete this.editedItem[key]
        }
      }
    },

    updateProductForm () {

    }
  }
}
</script>

<style lang="scss" scoped>
  .v-text-field__details {
    padding: 0;
  }
  .inner-select {
    max-width: fit-content !important;
    padding-top: 0 !important;
    margin-top: 0 !important;
  }
</style>

<style lang="scss">
  .variants .v-text-field__details {
    padding: 0 5px !important;
  }
  .currency-selector .v-input__append-inner {
    margin-top: 5px !important;

    .v-input__control > .v-input__slot:before {
      border: none;
    }
  }
</style>
