import axiosCustom from '.'

export default class FoodTypeServices {
  static getAllFoodTypes () {
    return axiosCustom.get('/service/store/categories')
  }

  static getFoodTypes (data) {
    return axiosCustom.get('/service/store/categories', { params: data })
  }

  static getFoodTypeById (data) {
    return axiosCustom.get(`/service/store/categories/${data._id}`)
  }

  static addFoodType (data) {
    return axiosCustom.post('/service/store/categories/', data)
  }

  static updateFoodType (data) {
    return axiosCustom.patch(`/service/store/categories/${data._id}`, data)
  }

  static deleteFoodType (data) {
    return axiosCustom.delete(`/service/store/categories/${data._id}`)
  }
}
