<template>
    <v-dialog
      v-model="dialog"
      scrollable
      persistent
      max-width="300px"

    >
      <v-card style="height: 300px !important;">
        <v-card-title>Donde buscar?</v-card-title>
        <v-divider></v-divider>
        <v-card-text style="height: 300px; padding-bottom: 0 !important;">
          <v-row>
            <v-col class="my-3" cols="12">
              <label>Localidad</label>
              <ValidationProvider
                v-slot="{ errors }"
                name="province"
                rules="required"
              >
                <!-- <label for="province">Provincia*</label> -->
                <v-select
                  ref="province"
                  id="province"
                  v-model="province"
                  :items="provinces"
                  item-text="name"
                  item-value="name"
                  placeholder="Provincia"
                  outlined
                  dense
                  class="mx-auto my-2 rounded-lg"
                  no-data-text="No hay datos"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                ></v-select>
              </ValidationProvider>

              <ValidationProvider
                v-slot="{ errors }"
                name="municipality"
                rules="required"
              >
                <!-- <label for="municipality">Municipio*</label> -->
                <v-select
                  ref="municipality"
                  id="municipality"
                  v-model="municipality"
                  :items="municipalities"
                  placeholder="Municipio"
                  outlined
                  dense
                  class="mx-auto rounded-lg"
                  no-data-text="No hay datos"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                ></v-select>
              </ValidationProvider>
            </v-col>
          </v-row>
          <!-- <v-divider></v-divider>
          <v-row>
            <v-col class="mt-3" cols="12">
              <label>Departamento</label>
              <v-btn-toggle
                v-model="department"
                tile
                color="deep-orange accent-3"
                group
                class="d-block pt-2"
              >
                <v-btn value="Hombre" outlined block class="rounded-lg" style="border: 1px solid grey; height: 40px !important;">
                  Hombre
                </v-btn>

                <v-btn value="Mujer" outlined block class="rounded-lg" style="border: 1px solid grey; height: 40px !important;">
                  Mujer
                </v-btn>

                <v-btn value="Nio" outlined block class="rounded-lg" style="border: 1px solid grey; height: 40px !important;">
                  Nio
                </v-btn>

                <v-btn value="Nia" outlined block class="rounded-lg" style="border: 1px solid grey; height: 40px !important;">
                  Nia
                </v-btn>
              </v-btn-toggle>
            </v-col>
          </v-row> -->
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="grey darken-1"
            text
            @click="search"
            class="text-bold"
            :disabled="municipality == null"
          >
            Buscar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
</template>

<script>
import provinces from '@/nomencladores/provinces.json'

export default {
  props: {
    showDialog: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      dialogm1: '',
      provinces: provinces,
      province: null,
      municipality: null,
      department: null,
      update: false
    }
  },
  mounted () {
    var storeSearch = localStorage.getItem('store_search')
    if (storeSearch) {
      storeSearch = JSON.parse(localStorage.getItem('store_search'))
      this.province = storeSearch.province
      this.municipality = storeSearch.municipality
      this.department = storeSearch.department
    } else {
      this.$store.dispatch('showSearchLocationDialog')
    }
  },
  computed: {
    municipalities: function () {
      if (this.province) {
        return this.provinces.find(p => p.name === this.province).municipalities
      }
      return []
    },

    dialog () {
      return this.$store.getters.searchLocationDialogStatus()
    }
  },
  methods: {
    search () {
      if (localStorage.getItem('store_search')) {
        const storeSearch = JSON.parse(localStorage.getItem('store_search'))
        if (this.municipality !== storeSearch.municipality) {
          this.$store.dispatch('setSearch', {
            province: this.province,
            municipality: this.municipality,
            department: this.department
          })
        }
      } else {
        this.$store.dispatch('setSearch', {
          province: this.province,
          municipality: this.municipality,
          department: this.department
        })
      }
      this.$store.dispatch('showSearchLocationDialog')
    }
  }
}
</script>
