<template>
  <v-card>
        <v-img
          :src="getImgUrl(store.photo)"
          :lazy-src="getImgUrl(store.photo)"
          max-height="202"
          min-height="202"
          width="100%"
        >
          <v-card-title :class="showInfo ? 'py-0 pt-2 text-h6 align-end white--text botton-title shadow-container-open' : 'py-0 text-h6 align-end white--text botton-title shadow-container'">
            <v-row class="mx-0">
              <v-col>
                <router-link :to="'/venta/'+store.slug">
                  <p class="ma-0 text-body-1 font-weight-bold font-italic text-left" id="section-title" >
                   {{ store.name }}
                  </p>
                  <p class="mb-2 pt-4 info-text white--text" v-if="showInfo">{{ store.phone }}</p>
                  <p class="mb-1 info-text white--text">{{ `${store.address.mainStreet}, ${store.address.municipality}, ${store.address.province}` }}</p>
                </router-link>
              </v-col>
              <v-col cols="auto" class="pr-0">
                <v-btn icon color="white" @click="showInfo = !showInfo">
                  <v-icon>{{ showInfo ? 'mdi-chevron-down' : 'mdi-chevron-up'}}</v-icon>
                </v-btn>
              </v-col>
            </v-row>
            <v-row justify="space-between" class="pb-3 f-10 align-end" v-if="showInfo">
              <v-col cols="auto">
                <v-chip color="green" pill outlined small>Abierto</v-chip>
              </v-col>
              <v-col cols="auto">
                <v-row justify="end" >
                  <v-btn color="white" class="mr-2 text-capitalize" outlined rounded small :to="'/venta/'+store.slug">Mapa</v-btn>
                  <v-btn color="white" class="text-capitalize" outlined rounded small :to="'/venta/'+store.slug">Ver venta</v-btn>
                </v-row>
              </v-col>
            </v-row>
          </v-card-title>
        </v-img>
      </v-card>
</template>

<script>
export default {
  props: {
    store: {
      type: Object,
      require: true
    }
  },
  data () {
    return {
      showInfo: false
    }
  },
  methods: {
    getImgUrl (image) {
      // return process.env.VUE_APP_API_IMAGE_URL + image
      var images = require.context('../../assets/home/', false)
      return images('./' + image)
    }
  }
}
</script>

<style lang="scss" scoped>
  .shadow-container {
    background-color: #232323a6;
    width: 100%;
    height: auto !important;
    .info-text {
      font-size: 12px !important;
      line-height: normal;
    }
  }
  .shadow-container-open {
    background-color: #232323db;
    width: 100%;
    height: 100% !important;
    .info-text {
      font-size: 13px !important;
      line-height: normal;
    }
  }
</style>
