        <section v-if="slideMode" id="products-slider-container" style="z-index: 2; position: relative;">
          <v-row justify="space-between" align="center" v-if="showInformation" :style="$vuetify.breakpoint.mobile ? 'margin: 0 1.5rem !important' : ''">
            <v-col cols="12">
              <router-link to="/venta/venta-de-yazmin"><span id="section-title" >Nombre de la venta</span></router-link>
              <v-rating
                background-color="grey"
                color="warning"
                hover
                length="5"
                size="18"
                :value="4.5"
                class="mb-2"
              ></v-rating>
              <p class="store-categories-info">Artesania y Decoracion, Ropa y Accesorios </p>
            </v-col>
          </v-row>

          <VueSlickCarousel style="height: fit-content; max-height: fit-content;"
            :class="[isMobile ? 'mx-3' : 'mx-0', 'mx-lg-10', 'mx-md-9', 'mx-sm-10', 'px-0', 'px-lg-10', 'px-md-3', 'px-sm-1']"
            :dots='false'
            :infinite='false'
            lazyLoad="ondemand"
            :adaptiveHeight='true'
            :initialSlide='0'
            :speed='500'
            :slidesToScroll='1'
            :touchThreshold='6'
            :swipeToSlide='true'
            :arrows="false"
            :responsive='[
                {
                  "breakpoint": 1904,
                  "settings": {
                    "slidesToShow": 4,
                    "slidesToScroll": 2,
                    "infinite": false,
                    "dots": false
                  }
                },
                {
                  "breakpoint": 1150,
                  "settings": {
                    "slidesToShow": 3,
                    "slidesToScroll": 2,
                    "infinite": false,
                    "dots": false
                  }
                },
                {
                  "breakpoint": 900,
                  "settings": {
                    "slidesToShow": 3,
                    "slidesToScroll": 3,
                    "initialSlide": 2
                  }
                },
                {
                  "breakpoint": 800,
                  "settings": {
                    "slidesToShow": 2,
                    "slidesToScroll": 2,
                    "initialSlide": 2,
                    "dots": false
                  }
                },
                {
                  "breakpoint": 750,
                  "settings": {
                    "slidesToShow": 2,
                    "slidesToScroll": 2,
                    "initialSlide": 2,
                  }
                },
                {
                  "breakpoint": 650,
                  "settings": {
                    "slidesToShow": 3,
                    "slidesToScroll": 2,
                    "initialSlide": 2,
                    "arrows": false,
                    "dots": true
                  }
                },
                {
                  "breakpoint": 480,
                  "settings": {
                    "slidesToShow": 2,
                    "slidesToScroll": 1
                  }
                }
              ]'>
              <ProductCarousel v-for="(product, i) in getProducts" :key="i" :product="product" :singleMode="!isMobile"/>
          </VueSlickCarousel>
          <!-- </div> -->
          <div class="slider-container" :style="`height: 300px; margin: 25px ${noProductsWidth}px 0; background: #ececec; border-radius: 11px`"  v-if="getProducts && getProducts.length === 0">
            <v-row style="height: 100%" align="center">
              <v-col align="center"><h3>No hay productos</h3></v-col>
            </v-row>
          </div>
          <!-- <h3 class="mt-16 text-center" style="color: #C0C4C7" v-if="products && products.length === 0">No hay nada en esta categoria</h3> -->
        </section>

  </div>
