<script>
import store from '../store'
import ProductCarousel from '@/components/Store/ProductCarousel'
// import Product from '@/components/Store/Product'
// import { apiServices } from '@/mixins/api.services'
import storeServices from '@/axios/store.services'

import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'

export default {
  name: 'SaleCarouselPreview',
  props: {
    slideMode: {
      type: Boolean,
      default: false
    },
    showInformation: {
      type: Boolean,
      default: true
    },
    listSize: {
      type: Number,
      required: false,
      default: 0
    },
    store: {
      type: Object,
      required: true,
      default: null
    },
    menu: {
      type: Array,
      required: false
    }
  },
  // mixins: [apiServices],
  components: {
    ProductCarousel,
    VueSlickCarousel
  },
  data () {
    return {
      pagination: {
        limit: 12,
        offset: 0,
        page: 1,
        filter: null,
        order: '-name',
        total: 0
      },
      products: [],
      pageProducts: []
    }
  },
  mounted () {
    this.products = []
    if (this.menu) {
      this.products = this.menu.filter(item => { return item.isActive === true })
    }

    if (!this.menu) {
      storeServices.getStoreOffers({ _id: this.store._id }).then((result) => {
        this.products = result.data.filter(item => { return item.isActive === true })
        // this.products.map((item) => {
        //   item.store = {
        //     _id: this.store._id,
        //     name: this.store.name
        //   }
        //   return item
        // })
      }).catch(() => {
        // this.$toast.error('Error al cargar las ofertas')
      })
    }

    if (this.store && this.menu && this.menu.length > 0) {
      if (this.slideMode && this.listSize > 0) {
        this.products = this.menu.slice(0, this.listSize)
      }
      this.pageProducts = this.menu.slice(this.pagination.offset, this.pagination.limit * this.pagination.page)
    }
  },
  computed: {
    amoutn: {
      get () {
        return store.getters.checkProductOnCart(this.product._id)
      }
    },
    concatFoodTypes: function () {
      var types = this.store.foodType.map((item) => {
        return item.type
      })
      // return this.store.foodType ? this.store.foodType.join(', ') : ''
      return types.join(', ')
    },
    getProducts () {
      if (this.menu) {
        this.products = []
        this.products = this.menu
      }
      // return this.products
      return this.$store.getters.products()
    },
    noProductsWidth () {
      return this.$vuetify.breakpoint.mobile && window.innerWidth < 960 ? 30 : 100
    },
    isMobile () {
      return this.$vuetify.breakpoint.mobile && (this.$vuetify.breakpoint.name === 'xs' || window.innerWidth < 500)
    },
    windowWidth () {
      return window.innerWidth
    }
  },
  methods: {
    addToCart (product) {
      this.$store.dispatch('addToCart', product)
    },
    checkOnFavorites (product) {
      if (product.isFavorite) {
        this.$store.dispatch('addToFavorites', product)
      } else {
        this.$store.dispatch('removeFromFavorites', product)
      }
      product.isFavorite = !product.isFavorite
    },
    checkOnCart (product) {
      return store.getters.checkProductOnCart(product._id)
    },
    getImgUrl (image) {
      var images = require.context('../assets', false)
      return images('./' + image)
    },
    seeDetails (product) {
      this.$store.dispatch('updateProductDetails', product)
    },
    paginate () {
      this.pagination.offset = this.pagination.limit * this.pagination.page - this.pagination.limit
      this.pageProducts = this.products.slice(this.pagination.offset, this.pagination.limit * this.pagination.page)
    }
  }
}
</script>

