<template>
  <v-main>
      <v-app-bar
        absolute
        color="white"
        elevate-on-scroll
        class="header-toolbar"
        style="position: fixed; z-index: 8; width: 100%;"
      >
        <back-button />

        <v-toolbar-title>Mi Calendario</v-toolbar-title>

        <v-spacer></v-spacer>

        <v-btn icon rounded @click="newSalePromotion" class="mr-3">
          <v-icon>mdi-plus</v-icon>
        </v-btn>
      </v-app-bar>
    <v-container class="mt-16">
      <v-row class="mx-0 mb-3">
        <v-col class="">
          <v-sheet height="64">
            <v-toolbar
              flat
            >
              <!-- <v-btn
                outlined
                class="mr-4"
                color="grey darken-2"
                @click="setToday"
              >
                Today
              </v-btn>
              <v-btn
                fab
                text
                small
                color="grey darken-2"
                @click="prev"
              >
                <v-icon small>
                  mdi-chevron-left
                </v-icon>
              </v-btn>
              <v-btn
                fab
                text
                small
                color="grey darken-2"
                @click="next"
              >
                <v-icon small>
                  mdi-chevron-right
                </v-icon>
              </v-btn> -->
              <v-spacer></v-spacer>
              <v-toolbar-title v-if="$refs.calendar">
                {{ $refs.calendar.title }}
              </v-toolbar-title>
              <v-spacer></v-spacer>
              <v-menu
                bottom
                right
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    outlined
                    color="grey lighten-2"
                    v-bind="attrs"
                    v-on="on"
                  >
                    <span>{{ typeToLabel[type] }}</span>
                    <v-icon right>
                      mdi-menu-down
                    </v-icon>
                  </v-btn>
                </template>
                <v-list>
                  <v-list-item @click="type = 'month'">
                    <v-list-item-title>Mes</v-list-item-title>
                  </v-list-item>
                  <v-list-item @click="type = 'day'">
                    <v-list-item-title>Dia</v-list-item-title>
                  </v-list-item>
                  <!-- <v-list-item @click="type = 'week'">
                    <v-list-item-title>Semana</v-list-item-title>
                  </v-list-item> -->
                  <!-- <v-list-item @click="type = '4day'">
                    <v-list-item-title>4 das</v-list-item-title>
                  </v-list-item> -->
                </v-list>
              </v-menu>
            </v-toolbar>
          </v-sheet>
          <v-sheet height="600">
            <v-calendar
              ref="calendar"
              v-model="focus"
              color="primary"
              :events="events"
              :event-color="getEventColor"
              :type="type"
              @click:event="showEvent"
              @click:more="viewDay"
              @click:date="viewDay"
              @change="updateRange"
            ></v-calendar>
            <v-dialog
              v-model="selectedOpen"
              :activator="selectedElement"
              scrollable
              max-width="300px"
            >
              <v-card
                color=""
                min-width="260px"
                flat
              >
              <v-card-title class="text-h5">
                {{ formatDate(selectedEvent.date) }}
              </v-card-title>
              <v-divider></v-divider>
                <v-card-text>
                  <h3 class="mt-4">{{ selectedEvent.name }}</h3>
                  <p class="text-bold mb-1 mt-4">Telfono:</p>
                  <span>{{ selectedEvent.phone }}</span>
                  <p class="text-bold mb-1 mt-4">Horario:</p>
                  <span>{{ getTime(selectedEvent) }}</span>
                  <p class="text-bold mt-4">Trabajo:</p>
                  <ul>
                    <li v-for="(work, i) in selectedEvent.works" :key="i">{{work.name}}</li>
                  </ul>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                  <v-btn text class="text-capitalize" @click="selectedOpen = false">
                    Cerrar
                  </v-btn>
                  <v-spacer></v-spacer>
                  <v-btn text color="red" class="text-capitalize">
                    <v-icon class="mr-2">mdi-delete</v-icon>
                    Borrar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-sheet>
        </v-col>
      </v-row>
    </v-container>
  </v-main>
</template>

<script>
import BackButton from '../../../components/BackButton.vue'
// import moment from 'moment'
// import BackButton from '../../components/BackButton.vue'

export default {
  name: 'Schedule',
  components: {
    BackButton
    // BackButton
  },
  data: () => ({
    focus: '',
    type: 'month',
    typeToLabel: {
      month: 'Mes',
      week: 'Semana',
      day: 'Dia',
      '4day': '4 Das'
    },
    selectedEvent: {},
    selectedElement: null,
    selectedOpen: false,
    events: [],
    colors: ['blue', 'indigo', 'deep-purple', 'cyan', 'green', 'orange', 'grey darken-1']
    // names: ['Meeting', 'Holiday', 'PTO', 'Travel,hkjhkjh kjhkjhkjhkjhk', 'Event', 'Birthday', 'Conference', 'Party']
  }),
  mounted () {
    this.$refs.calendar.checkChange()
  },
  computed: {
    municipalities: function () {
      if (this.editedItem.address.province) {
        return this.provinces.find(p => p.name === this.editedItem.address.province).municipalities
      }
      return []
    }
  },
  methods: {
    showDrawer () {
      this.$store.dispatch('showDrawer', !this.$store.getters.showDrawer())
    },
    viewDay ({ date }) {
      this.focus = date
      this.type = 'day'
    },
    getEventColor (event) {
      return event.color
    },
    setToday () {
      this.focus = ''
    },
    prev () {
      this.$refs.calendar.prev()
    },
    next () {
      this.$refs.calendar.next()
    },
    showEvent ({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event
        // this.selectedEvent.id = 1
        this.selectedElement = nativeEvent.target
        requestAnimationFrame(() => requestAnimationFrame(() => {
          // this.selectedOpen = true
          this.$router.push({ name: 'sale-promotion', params: { event: event } })
          return true
        }))
      }

      if (this.selectedOpen) {
        this.selectedOpen = false
        requestAnimationFrame(() => requestAnimationFrame(() => open()))
      } else {
        open()
      }

      nativeEvent.stopPropagation()
    },
    updateRange ({ start, end }) {
      let events = []

      // const min = new Date(`${start.date}T00:00:00`)
      // const max = new Date(`${end.date}T23:59:59`)
      // const days = (max.getTime() - min.getTime()) / 86400000
      // const eventCount = this.rnd(days, days + 20)

      // for (let i = 0; i < eventCount; i++) {
      //   const allDay = this.rnd(0, 3) === 0
      //   const firstTimestamp = this.rnd(min.getTime(), max.getTime())
      //   const first = new Date(firstTimestamp - (firstTimestamp % 900000))
      //   const secondTimestamp = this.rnd(2, allDay ? 288 : 8) * 900000
      //   const second = new Date(first.getTime() + secondTimestamp)

      //   events.push({
      //     name: this.names[this.rnd(0, this.names.length - 1)],
      //     start: first,
      //     end: second,
      //     color: this.colors[this.rnd(0, this.colors.length - 1)],
      //     timed: !allDay
      //   })

      //   console.log(events)
      // }

      // events = [
      //   {
      //     name: 'Lya',
      //     phone: '56326886',
      //     start: '2022-01-20 15:30',
      //     end: '2022-01-20 16:45',
      //     color: this.colors[this.rnd(0, this.colors.length - 1)],
      //     timed: true,
      //     details: 'Asda s dasd asd asd asd asd asd asd asd'
      //   },
      //   { name: 'Ernesto', start: '2022-01-20 10:30', end: '2022-01-20 12:45', color: this.colors[this.rnd(0, this.colors.length - 1)], timed: false }
      // ]

      events = this.$store.getters.events()
      events = events.map(x => {
        x.color = this.colors[this.rnd(0, this.colors.length - 1)]
        return x
      })

      this.events = events
    },
    rnd (a, b) {
      return Math.floor((b - a + 1) * Math.random()) + a
    },

    addNotificationEvent (event) {
      if (!event.toNotify) {
        this.$store.dispatch('addNotification', event)
      } else {
        this.$store.dispatch('removeNotification', event)
      }
      event.toNotify = !event.toNotify
    },

    toNotify (event) {
      return this.$store.getters.existNotification(event.id)
    },

    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`

      // return moment(date, 'YYYY-MM-DD HH:mm').format('DD/MM/YYYY HH:mm')
    },

    getTime (event) {
      if (event && event.start && event.end) {
        const start = event.start.split(' ')[1]
        const end = event.end.split(' ')[1]
        return start + ' - ' + end
      }
    },

    newSalePromotion () {
      this.$router.push({ name: 'sale-promotion', params: {} })
    },

    editSalePromotion () {
      this.$router.push({ name: 'sale-promotion', params: {} })
    },

    reset () {
      for (const key in this.$refs) {
        if (key !== 'observer') {
          this.$refs[key].reset()
        }
      }
    },

    updateCoordenates (event) {
      this.editedItem.address.coordenates = event
      this.invalidCoordenates = this.editedItem.address.coordenates === null
    }
  }
}
</script>
