            </v-col>
            <v-col cols="8" class="px-0 ps-md-3 px-sm-3 d-flex flex-column">
              <v-row justify="space-between" class="mr-0">
                <v-col class="pa-0">
                  <h2 class="text-bold f-14">Nombre del producto</h2>
                </v-col>
                <v-col cols="auto" icon class="pt-0 pr-0">
                  <v-icon>mdi-pencil</v-icon>
                </v-col>
              </v-row>
              <v-row justify="end" class="f-10 align-end mr-0">
