                  <v-switch
                    color="success"
                    hide-details
                    label="Disponible"
                  >
                  </v-switch>
                  <v-switch
                    color="success"
                    hide-details
                    class="mt-2"
                  >
                  <template v-slot:label>
                    <span class="f-12">Vendido</span>
                  </template>
                  </v-switch>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-card> -->

        <v-card class="product-list-card-template">
          <v-row>
            <v-col class="img-col px-0 pt-3" cols="5" align="center" align-self="top">
              <v-img max-height="125" contain src="../../../../assets/casa-en-venta.png" lazy-src="../../../../assets/casa-en-venta.png"></v-img>
              <div id="price" class="my-2 text-center">150.00 <span class="currency-label"> CUP</span></div>
            </v-col>
            <v-col cols="7" class="px-0 ps-md-3 px-sm-3 d-flex flex-column">
              <v-row justify="space-between" class="mr-0">
                <v-col class="pa-0">
                  <h2 class="text-bold f-14 text-2-line-truncate">Nombre del producto</h2>
                </v-col>
              </v-row>
              <v-row justify="end" class="f-10 align-end mr-0">
                <v-col cols="auto" icon class="pt-0 pr-0">
                  <v-btn outlined height="21" width="100" max-width="100">Editar</v-btn>
                </v-col>
                <v-col cols="auto" class="px-0">
                  <v-switch
                    color="success"
                    hide-details
                    label="Disponible"
                    class="mt-2"
                  >
                  </v-switch>
                  <v-switch
                    color="success"
                    hide-details
                    class="mt-2"
                    label="Vendido"
                  >
                  </v-switch>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-card>
      </v-container>
    </v-main>
</template>

<script>
import { dateRules } from '@/mixins/date_rules'
import { numberRules } from '@/mixins/number_rules'
import { generalRules } from '@/mixins/general_rules'
import { apiServices } from '@/mixins/api.services'
// import offersServices from '@/axios/offers.services'
import storeServices from '@/axios/store.services'
import userServices from '@/axios/user.services'
import BackButton from '../../../../components/BackButton.vue'

export default {
  name: 'MenuAdministration',
  mixins: [numberRules, dateRules, generalRules, apiServices],
  components: {
    BackButton
  },
  props: {
    products: {
      type: Array,
      require: true
    }
  },
  data () {
    return {
      search: '',
      selected: [],
      address: '',
      createNewMenu: false,
      loading: false,
      dialog: false,
      dialogDelete: false,
      editedIndex: -1,
      editedItem: {
        _id: null,
        isActive: true,
        name: null,
        slug: null,
        description: null,
        price: null,
        category: null,
        variants: null,
        image: null,
        add: [],
        store: null
      },
      defaultItem: {
        _id: null,
        isActive: true,
        name: null,
        slug: null,
        description: null,
        price: null,
        category: null,
        variants: null,
        image: null,
        add: [],
        store: null
      },
      selectedRestaurant: null,
      options: {
        duration: 300,
        offset: 150,
        easing: 'easeInOutCubic'
      }
    }
  },
  watch: {
    dialog (val) {
      val || this.close()
    },

    dialogDelete (val) {
      val || this.closeDelete()
    }
  },
  created () {
  },
  mounted () {
    // if (!this.isAdmin) {
    //   this.loadUserRestaurants()
    // } else {
    //   this.loadRestaurantNames()
    // }
    // this.loadCategories()
  },
  computed: {
    headers () {
      return [
        {
          text: '',
          align: 'center',
          sortable: false,
          value: 'image',
          filterable: false
        },
        {
          text: 'Nombre',
          align: 'start',
          sortable: true,
          value: 'name'
        },
        {
          text: 'Precio',
          align: 'center',
          sortable: true,
          value: 'price'
        },
        {
          text: 'Categora',
          align: 'center',
          value: 'category.name',
          sortable: true
          // filter: value => {
          //   return value.name.includes(this.search) || false
          // }
        },
        { text: 'Disponible', align: 'start', value: 'isActive', sortable: true },
        { text: '', align: 'center', value: 'actions', sortable: false }
      ]
    },
    isAdmin () {
      return this.$store.getters.isAdmin()
    }
  },
  methods: {
    customizedFilter (value, search, item) {
      search = search.toLowerCase()
      if (item.name.toLowerCase().replaceAll(/ /g, '-').replaceAll(/[^\w-]+/g, '').includes(search)) {
        return true
      }
      if (Number.parseFloat(item.price).toFixed(2).toString().toLowerCase().includes(search)) {
        return true
      }
      if (item.category.name.toLowerCase().includes(search)) {
        return true
      }

      return false
    },

    loadUserRestaurants () {
      this.loading = false
      userServices.getUserRestaurants().then((result) => {
        this.userSales = result.data.stores
      }).catch(() => {
        this.$toast.error('Error al cargar los stores')
      }).finally(() => (this.loading = false))
    },

    createProduct () {
      this.$router.push({ name: 'create-product', params: { } })
    },

    editProduct () {
      this.$router.push({ name: 'edit-product', params: { product: this.defaultItem.products } })
    },

    editItem (item) {
      this.editedIndex = this.menu.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.createNewMenu = true
    },

    deleteItem (item) {
      this.editedIndex = this.menu.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm () {
      storeServices.deleteRestaurantOffer({ storeId: this.selectedRestaurant, offerId: this.editedItem._id }).then((result) => {
        if (result) {
          this.menu.splice(this.editedIndex, 1)
          this.closeDelete()
          this.$toast.success('Oferta eliminado!')
        }
      }).catch(() => {
        this.$toast.error('No se pudo eliminar la oferta')
      })
    },

    deleteSelectedOffers () {
      this.selected.forEach(offer => {
        storeServices.deleteRestaurantOffer({ storeId: this.selectedRestaurant, offerId: offer._id }).then((result) => {
          if (result) {
          }
        }).catch(() => {
          this.$toast.error('No se pudo eliminar la oferta')
        })
      })
      // this.loadRestaurants()
      this.loadRestaurantOffers()
      this.selected = []
      this.dialog = false
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.$vuetify.goTo('#menu', this.options)
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
      this.createNewMenu = false
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {
      // if (this.editedIndex > -1) {
      //   Object.assign(this.menu[this.editedIndex], this.editedItem)
      // } else {
      //   this.menu.push(this.editedItem)
      // }
      this.$nextTick(() => {
        this.loadRestaurantOffers()
        this.$vuetify.goTo('#menu', this.options)
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
      this.createNewMenu = false
    },

    getImgUrl (item) {
      return process.env.VUE_APP_API_IMAGE_URL + item.image
    },

    category (value) {
      var range = this.categories.find(f => {
        return f._id === value
      })
      if (range) {
        range = range.type
      }
      return range
    },

    storeSelected () {
      if (this.selectedRestaurant) {
        this.loadRestaurantOffers()
        this.defaultItem.store = this.selectedRestaurant
        this.editedItem.store = this.selectedRestaurant
      } else {
        this.menu = []
      }
    },

    loadRestaurantOffers () {
      this.loading = true
      storeServices.getStoreOffers({ _id: this.selectedRestaurant }).then((result) => {
        this.menu = result.data
      }).catch(() => {
        this.$toast.error('Error al cargar las ofertas')
      }).finally(() => (this.loading = false))
    },

    changeStatus (item) {
      this.changeOfferStatus(item)
    },

    searchRestaurant (event) {
      this.loadAdminRestaurants({ params: { name: event.target.value } })
    }
  }
}
</script>

