  font-size: 0.5rem;
  letter-spacing: 1.5px;
}
.card-product-name {
  font-size: 14px !important;
  line-height: normal !important;
}
.card-template{
  margin: 0 50px;
}
p {
  margin-bottom: 0;
}
.f-10 {
  font-size: 10px;
}
.f-12 {
  font-size: 12px !important;
}
