<template>
  <v-main>
      <v-container>
        <v-col id="login-form" style="width: 437px; height: auto">
          <v-card class="rounded-xl">
            <v-col align-self="center">
              <h1 class="text-bolder text-center mb-8">INICIAR SESIN</h1>
              <ValidationObserver ref="observer">
                <form  @submit.prevent="login">
                  <v-row align="center">
                    <v-col>
                      <ValidationProvider
                        v-slot="{ errors }"
                        name="email"
                        ref="email"
                        rules="required"
                      >
                        <!-- <label class="text-left" for="email">Usuario</label> -->
                        <v-text-field
                          id="user_email"
                          v-model="user.email"
                          label="Correo"
                          outlined
                          dense
                          class="mx-auto rounded-lg"
                          :error-messages="errors"
                          :hide-details="errors.length > 0 ? false : true"
                        >
                        </v-text-field>
                      </ValidationProvider>
                    </v-col>
                  </v-row>
                  <v-row  align="center">
                    <v-col>
                      <ValidationProvider
                        v-slot="{ errors }"
                        name="password"
                        rules=""
                      >
                        <!-- <label class="text-left" for="password">Contrasea</label> -->
                        <v-text-field
                          ref="password"
                          id="password"
                          v-model="user.password"
                          label="Contrasea"
                          outlined
                          dense
                          :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                          :type="show1 ? 'text' : 'password'"
                          class="mx-auto rounded-lg"
                          :error-messages="errors"
                          :hide-details="errors.length > 0 ? false : true"
                          @click:append="show1 = !show1"
                        >
                        </v-text-field>
                      </ValidationProvider>
                    </v-col>
                  </v-row>
                  <v-row align="center" justify="space-around">
                    <v-col align="center">
                      <p class="text-info"><span class="link" @click="forgotPassword">Olvid la contrasea?</span></p>
                    </v-col>
                  </v-row>
                  <v-row align="center" justify="space-around">
                    <v-col align="center">
                      <v-btn type="submit" class="rounded-lg text-bold shadow" color="secondary">Entrar</v-btn>
                    </v-col>
                  </v-row>
                  <v-row align="center" justify="space-around">
                    <v-col align="center">
                      <p>Si eres nuevo, <router-link class="text-info" to="/registrarse">Regstrate</router-link></p>
                    </v-col>
                  </v-row>
                </form>
              </ValidationObserver>
            </v-col>
          </v-card>
        </v-col>
      </v-container>
  </v-main>
</template>

<script>
import authenticationServices from '@/axios/authentication.services'
import router from '../../router'

export default {
  name: 'Login',
  data () {
    return {
      show1: false,
      user: {
        email: null,
        password: null
      }
    }
  },
  created () {
    // var storedUser = localStorage.getItem('store_user')
    // if (storedUser) {
    //   storedUser = JSON.parse(storedUser)
    //   this.user.email = storedUser.email
    // }
  },
  methods: {
    login () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          authenticationServices.login(this.user).then((result) => {
            if (result) {
              this.$store.dispatch('logIn', result.data)
              this.$toast.success('Bienvenido')
              router.push({ path: '/' })
            }
          }).catch((err) => {
            if (err.response.status === 403 && err.response.data.name === 'NotActive') {
              router.push({ name: 'confirmation', params: { sendEmail: true, user: this.user } })
            } else if (err.response.status === 404 && err.response.data.name === 'UserNotFound') {
              this.$toast.error('Usuario o contrasea incorrecta')
            } else {
              this.$toast.error('Error de iniciar')
            }
          })
        }
      })
    },
    forgotPassword () {
      this.$refs.observer.reset()
      this.$refs.email.validate().then((result) => {
        if (result.valid) {
          authenticationServices.reSendEmail({ type: 2, email: this.user.email }).then((result) => {
            if (result) {
              router.push({ name: 'forgotPassword', params: { user: this.user } })
              this.$toast.success('Se ha enviado un cdigo a su correo')
            }
          }).catch((err) => {
            if (err.response.data.name === 'UserNotFound') {
              this.$toast.error('El correo insertado no existe')
            } else {
              this.$toast.error('No se pudo enviar el cdigo de verificacin')
            }
          })
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.v-main{
  background: lightsalmon;
  // background-image: url('../../assets/login/Background.png') !important;
  // background-size: cover;
  /* height: 85%; */
}

#login-form{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}

.row{
  margin: 0
}

.link{
  cursor: pointer;
}
</style>
